import GeneralUtils from './GeneralUtils'
import { API } from './API'

declare module '~/core/API' {
  interface API {
    Notification: ReturnType<typeof Notification.initAPI>
  }
}

const log = GeneralUtils.createScopedLog('Notification')

let api: API

/**
 * Functions showing desktop notifications for elapsed time and Nuclear Option
 *
 * @author              Warren Benedetto <wmbenedetto@gmail.com>
 * @since               March 27, 2010
 * @version             1.2.4
 */

const Notification = {
  settings: [300, 60, 10], // seconds
  NOTIFICATION_TTL: 5000, // milliseconds

  /**
   * Initializes API
   */
  initAPI: function (theAPI: API) {
    api = theAPI

    return api.mixin('Notification', {
      initAsync: this.initAsync.bind(this),
      get: this.get.bind(this),
      saveSettings: this.saveSettings.bind(this),
    })
  },

  /**
   * Initializes Notification class
   */
  init: function (callback: Function | undefined) {
    log('init', 'Initializing Notification', null, 'DEBUG')

    this.loadSettings()

    if (typeof callback === 'function') {
      callback()
    }
  },
  initAsync() {
    return new Promise((resolve) => this.init(resolve))
  },

  /**
   * Gets notification settings
   */
  get: function () {
    return this.settings
  },

  /**
   * Shows desktop notification
   *
   * @param type The type of notification to show
   */
  show: function (type: 'nuclear' | 'block') {
    log('show', 'Showing ' + type + ' notification')

    let icon = ''
    let message = ''
    let totalSecondsRemaining = 0

    if (type === 'nuclear') {
      totalSecondsRemaining = api.NuclearOption.getSecondsUntilActive()

      icon = '/common/img/icon-nuclear-48.png'
      message =
        api.Chrome.Translation.get('nuclearNotification') ||
        'The Nuclear Option will start blocking sites in'
    } else if (type === 'block') {
      totalSecondsRemaining = api.StayFocusd.getTotalSecondsRemaining()

      icon = '/common/img/icon-red-48.png'
      message =
        api.Chrome.Translation.get('blockNotification') ||
        'StayFocusd will start blocking your Blocked Sites in'
    }

    message +=
      totalSecondsRemaining > 60
        ? ' ' + api.Date.secondsToMinsAndSecs(totalSecondsRemaining)
        : ' ' + totalSecondsRemaining + ' seconds'

    const options: chrome.notifications.NotificationOptions<true> = {
      type: 'basic',
      title: 'StayFocusd',
      iconUrl: icon,
      message: message,
    }

    api.Chrome.Notification.create('', options, (notificationID: string) => {
      /* Close notification after NOTIFICATION_TTL seconds */
      if (notificationID != undefined)
        setTimeout(() => {
          api.Chrome.Notification.clear(notificationID)
        }, this.NOTIFICATION_TTL)
    })
  },

  /**
   * Checks whether a notification is set to appear at the given time
   *
   * @param timeToCheck The time to check, in seconds
   */
  isset: function (timeToCheck: number) {
    return this.settings.inArray(timeToCheck)
  },

  /**
   * Saves settings
   *
   * @param settings Object containing Notification settings
   */
  saveSettings: function (settings: number[]) {
    log('saveSettings', 'Saving settings', settings, 'TRACE')

    this.settings = settings

    api.Settings.set({ notificationSettings: settings })
  },

  /**
   * Loads settings
   */
  loadSettings: function () {
    log('loadSettings', 'Loading settings', null, 'TRACE')

    const notificationSettings = api.Settings.get('notificationSettings')

    if (notificationSettings) {
      this.settings = notificationSettings
    }
  },
}

export default Notification
