import * as WebExtUsage from '@sensortower/web-ext-usage'
import AppConsts from '~/core/AppConsts'
import * as PanelAPIUtils from '@sensortower/panel-api-utils'
import * as AdFinder from '@sensortower/ad-finder'
import GeneralUtils from './GeneralUtils'
import { API } from './API'
import { IndexedDbUsageDatabase } from '@sensortower/web-ext-usage'
import { seedDatabase } from '../utils/database/seed'

declare module '~/core/API' {
  interface API {
    WebUsage: ReturnType<typeof WebUsage.initAPI>
  }
}

const log = GeneralUtils.createScopedLog('WebUsage')

let api: API

const CRAWL_UPLOAD_ENDPOINT =
  'https://api-pm.stayfocusd.st-panel-api.com/Ajax0001/IPD'
const PM_CONFIG_ENDPOINT =
  'https://api-pm.stayfocusd.st-panel-api.com/Ajax0001/Config'
const STAYFOCUSD_AD_FINDER_PARTNER_ID = 33

const WebUsage = {
  initialized: null as Promise<void> | null,
  usageDatabase: null as IndexedDbUsageDatabase | null,

  initAPI(theAPI: API) {
    api = theAPI

    return api.mixin('WebUsage', {
      init: this.init.bind(this),
      getUsageDatabase: this.getUsageDatabase.bind(this),
    })
  },

  async init(): Promise<void> {
    if (this.initialized != null) {
      log(
        'init',
        'WebUsage already initialized',
        { sessionId: '' }, // TODO - add sessionId if possible
        'DEBUG',
      )
    } else {
      const isEnabled = async (): Promise<boolean> => {
        const hasAcceptedToS = await api.Storage.get('hasAcceptedToS', true)
        return hasAcceptedToS ?? false
      }
      const installId = api.Storage.get('install-id', true)

      const { promise, resolve } = api.Utils.getDecoupledPromise()
      this.initialized = promise

      // Start tracking usage
      this.usageDatabase = WebExtUsage.openIdbUsageDatabase({
        getInstallId: () => installId,
      })

      if (!MINIFIED) {
        await seedDatabase(this.usageDatabase!)
      }

      WebExtUsage.initUsageSdkInBackground({
        db: this.usageDatabase,
        enabled: isEnabled,
        isOnStartupAvailable: () => true,
        isTabActiveWhenPlayingAudio: () => true,
        logger: MINIFIED ? null : console,
      })

      resolve()
      log?.('init', 'Initialized WebUsage', this, 'INFO')

      const { processAdFinderMessage } =
        await AdFinder.initAdFinderInBackground({
          isEnabled: isEnabled,
          getInstallId: () => installId,
          extensionVersion: api.Chrome.Extension.getVersion(),
          extensionId: AppConsts.APP_ID,
          crawlUploadUrl: CRAWL_UPLOAD_ENDPOINT,
          pmExternalConfigUrl: PM_CONFIG_ENDPOINT,
          panelPartnerId: STAYFOCUSD_AD_FINDER_PARTNER_ID,
        })

      chrome.runtime.onConnect.addListener((port) => {
        if (port.name != 'ad-finder') return

        port.onMessage.addListener(async (msg) => {
          if (msg.message == 'forward-message.request') {
            const response = await processAdFinderMessage(msg.payload)

            port.postMessage({
              message: 'forward-message.response',
              payload: response,
              nonce: msg.nonce,
            })
          }

          if (msg.message == 'ad-found') {
            const hostname = PanelAPIUtils.parseUrl(msg.payload.hostname)
            if (!hostname) return
            const ad = msg.payload.ad

            const activeSessions =
              await this.usageDatabase?.sessionsRepo?.getActiveSessions()
            const tabSession = activeSessions?.find(
              (session) => session.appId === hostname.hostname,
            )
            if (!tabSession) return

            await this.usageDatabase?.adsRepo?.upsertAd({
              ...ad,
              sessionId: tabSession.id,
            })
          }
        })
      })
    }
  },

  async getUsageDatabase(): Promise<IndexedDbUsageDatabase> {
    await this.initialized
    if (this.usageDatabase == null) throw Error('WebUsage not initialized')
    return this.usageDatabase
  },
}

export default WebUsage
