import { isHostnameBlockedPredicate } from '~/utils/isHostnameBlocked'
import { API } from './API'

declare module '~/core/API' {
  interface API {
    WhitelistRepository: ReturnType<typeof WhitelistRepository.initAPI>
  }
}

// set a new name, because current obj with whitelist has the same name, but contains list of objects
const WHITELIST_IN_STORAGE_OLD = 'whitelist'
const WHITELIST_IN_STORAGE = 'whitelistNew'

let api: API
let _websites: string[] = []

const WhitelistRepository = {
  initAPI(theAPI: API) {
    api = theAPI

    return api.mixin('WhitelistRepository', {
      init: this.init.bind(this),
      add: this.add.bind(this),
      getAll: this.getAll.bind(this),
      isExists: this.isExists.bind(this),
      delete: this.delete.bind(this),
      migrate: this.migrate.bind(this),
      importWhitelist: this.importWhitelist.bind(this),
    })
  },

  async init() {
    const syncDisabled = (await API.Settings.get('disableSync')) === true

    if (syncDisabled) {
      const data = await api.Chrome.Storage.getSync(WHITELIST_IN_STORAGE)
      const hasDataForMigration = !!data[WHITELIST_IN_STORAGE]
      await API.Storage.setBucket(
        WHITELIST_IN_STORAGE,
        'LOCAL',
        !hasDataForMigration,
      )
    } else {
      const data = await api.Chrome.Storage.getLocal(WHITELIST_IN_STORAGE)
      const hasDataForMigration = !!data[WHITELIST_IN_STORAGE]
      await API.Storage.setBucket(
        WHITELIST_IN_STORAGE,
        'SYNC',
        !hasDataForMigration,
      )
    }

    this.getWhitelist()
    this.addListeners()
  },

  getWhitelist() {
    const websites = api.Settings.get(WHITELIST_IN_STORAGE)
    _websites = websites == null ? [] : websites
  },

  /**
   * Add a new website
   * Returns the flag whether the site was successfully added or not
   * @returns {Promise<boolean>}
   */
  async add(websites: string[]) {
    let isAdded = false
    for (let index = 0; index < websites.length; index++) {
      const website = websites[index]
      if (website && _websites.find((x) => x == website) == null) {
        isAdded = true
        _websites.push(website)
        await this.saveChanges()
      }
    }

    return isAdded
  },

  /**
   * Return whitelist
   * @returns {string[]}
   */
  getAll() {
    return _websites
  },

  /**
   * Delete website
   * @returns {Promise<void>}
   */
  async delete(website: string): Promise<void> {
    const targetIndex = _websites.findIndex((x) => x == website)
    if (targetIndex > -1) {
      _websites.splice(targetIndex, 1)
      await this.saveChanges()
    }
  },

  /**
   * Is whitelist contains website
   * @returns {void}
   */
  isExists(domain: string) {
    if (domain == null) return false

    domain = domain.toLowerCase()
    return (
      _websites.find((website) =>
        isHostnameBlockedPredicate(website, domain),
      ) != undefined
    )
  },
  /**
   * Migrate all whitelist websites to the new structure in the storage
   * @returns {Promise<void>}
   */
  async migrate() {
    if (this.needMigrate()) {
      const websitesObj = api.Settings.get(WHITELIST_IN_STORAGE_OLD)
      if (websitesObj != null) {
        const websites = Object.keys(websitesObj)
        await this.add(websites)
      }
    }
  },

  needMigrate() {
    return api.Settings.get(WHITELIST_IN_STORAGE) == null
  },

  /**
   * Move all whitelist websites to the new structure in the storage after import settings
   * @returns {Promise<void>}
   */
  async importWhitelist() {
    // we shoud check whitelist after import settings, because old and new whitelist have different structure
    _websites = []
    const websitesObj = api.Settings.get(WHITELIST_IN_STORAGE_OLD)
    if (websitesObj != null) {
      const websites = Object.keys(websitesObj)
      await this.add(websites)
    }
  },

  async saveChanges() {
    await api.Storage.set({
      [WHITELIST_IN_STORAGE]: _websites,
    })
  },

  addListeners() {
    API.PubSub.listen(
      'Settings.data.refreshed',
      async (message: string, payload: any) => {
        if (payload.changes.whitelistNew || payload.changes.disableSync) {
          if (payload.changes.disableSync) {
            if (payload.disableSync === false) {
              await api.Storage.setBucket(WHITELIST_IN_STORAGE, 'SYNC')
            } else {
              await api.Storage.setBucket(WHITELIST_IN_STORAGE, 'LOCAL')
            }
          }

          this.getWhitelist()
        }
      },
    )
  },
}

export default WhitelistRepository
