import { API } from '~/core/API'
import GeneralUtils from './GeneralUtils'

declare module '~/core/API' {
  interface API {
    Domain: ReturnType<typeof DomainParser.initAPI>
  }
}

const log = GeneralUtils.createScopedLog('DomainParser')

/**
 * Functions for parsing web domains: extracting base domains, full domains,
 * subdomains, url segments, etc.
 *
 * @author              Warren Benedetto <wmbenedetto@gmail.com>
 * @since               January 15, 2010
 * @version             1.0.0
 */
const DomainParser = {
  /**
   * Initializes Domain API
   *
   * @param API
   */
  initAPI(API: API) {
    return API.mixin('Domain', {
      extractBaseDomain: this.extractBaseDomain.bind(this),
      extractFullDomain: this.extractFullDomain.bind(this),
      isMoreGeneralURL: this.isMoreGeneralURL.bind(this),
      matchesWildcard: this.matchesWildcard.bind(this),
    })
  },

  /**
   * Extracts the full domain from the url, including subdomain
   *
   * @param url The url from which to get the domain
   * @return The domain
   */
  extractFullDomain: function (url: string) {
    if (!url) {
      return null
    }

    log('extractFullDomain', 'Extracting full domain from', url, 'TRACE')

    const matches = url.match(/^(.[^/]*?:\/\/)?(.[^/]+)/)

    if (matches === null || matches.length < 2) {
      const urlArray = url.split('/')

      return urlArray[0]
    }

    let match = matches[2]
    if (match.startsWith('www.')) {
      match = match.replace('www.', '')
    }

    return match
  },

  /**
   * Extracts the base domain from the url, with subdomain stripped
   *
   * @param url The url from which to get the domain
   * @return The domain
   */
  extractBaseDomain: function (url: string) {
    log('extractBaseDomain', 'Extracting base domain from', url, 'TRACE')

    const fullDomain = this.extractFullDomain(url)

    if (!fullDomain || fullDomain.length === 0) {
      return ''
    }

    const domainArray = fullDomain.split('.')

    if (domainArray.length === 1) {
      return fullDomain
    }

    /* Add last element from full domain to base domain. Could be like .com, or the .uk from .co.uk */
    let baseDomain = domainArray[domainArray.length - 1]

    /* Add penultimate element from full domain to base domain. Could be like stayfocusd from stayfocusd.com,
     * or the .co from .co.uk */
    baseDomain = domainArray[domainArray.length - 2] + '.' + baseDomain

    /* If this is a multi-part domain like stayfocusd.co.uk or stayfocusd.com.mx, include the third
     * part in the base domain. */
    if (this.isMultiPartDomain(fullDomain)) {
      baseDomain = domainArray[domainArray.length - 3] + '.' + baseDomain
    }

    return baseDomain
  },

  /**
   * Checks whether domain is a multi-part domain like .co.uk, .com.mx,
   * .mil.jp, etc.
   *
   * This will not catch every domain known to man. There are thousands
   * of possibilities, especially when getting into international domains
   * with longer words, foreign spellings, and special characters.
   *
   * This will only to catch the most common patterns, which is fine for
   * the purposes of this extension.
   *
   * @param fullDomain The full domain, i.e. www.stayfocusd.com or www.foo.bar.co.jp
   */
  isMultiPartDomain: function (fullDomain: string) {
    const logMsg = 'Is ' + fullDomain + ' multi-part domain?'

    const domainArray = fullDomain.split('.')
    let domainTokens = []

    if (domainArray.length === 1) {
      log('isMultiPartDomain', logMsg, 'NO', 'TRACE')

      return false
    }

    /* Get penultimate element from full domain. Could be like the stayfocusd from stayfocusd.com,
     * or the .co from .co.uk */
    const penultimate = domainArray[domainArray.length - 2]

    /* Penultimate can be a single character, like .a.se (yes, this exists) */
    if (penultimate.length === 1) {
      log('isMultiPartDomain', logMsg, 'YES', 'TRACE')
      return true
    }

    /* Penultimate can be two characters, like .co.uk. However, domains can be two characters
     * as well, like go.com. Therefore, we look for two chars plus more than 3 parts to a domain.
     * That would exclude go.com or www.go.com, while including www.go.co.uk */
    if (penultimate.length === 2 && domainArray.length > 3) {
      log('isMultiPartDomain', logMsg, 'YES', 'TRACE')
      return true
    }

    /* Penultimate can be a tld, like .com.cn or .net.mx. There are other 3-letter possibilities,
     * but these are the most common */
    if (penultimate.length === 3 && domainArray.length >= 3) {
      domainTokens = [
        'com',
        'edu',
        'gov',
        'mil',
        'net',
        'org',
        'biz',
        'www',
        'pro',
        'int',
        'web',
        'xxx',
      ]

      for (const key in domainTokens) {
        if (
          domainTokens.hasOwnProperty(key) &&
          penultimate === domainTokens[key]
        ) {
          log('isMultiPartDomain', logMsg, 'YES', 'TRACE')
          return true
        }
      }
    }

    /* Penultimate can occasionally be four letters. There are other possibilities,
     * but these are the most common */
    if (penultimate.length === 4 && domainArray.length >= 3) {
      domainTokens = [
        'info',
        'mobi',
        'name',
        'jobs',
        'kiwi',
        'aero',
        'asia',
        'coop',
      ]

      for (const key2 in domainTokens) {
        if (
          domainTokens.hasOwnProperty(key2) &&
          penultimate === domainTokens[key2]
        ) {
          log('isMultiPartDomain', logMsg, 'YES', 'TRACE')

          return true
        }
      }
    }

    /**
     * Ugh. This is rough. If we got this far, we might have something like bbc.co.uk ... but we might have
     * something like www.dn.se. The first is a multi-part domain; the second isn't. But they both have a
     * 3-letter antepenultimate. Therefore, the only way to distinguish them is to see if the antepenultimate
     * is www. Of course, this rule will break if the domain is foo.dn.se. Let's hope that never happens. :-P
     */
    if (domainArray.length >= 3) {
      const antepenultimate = domainArray[domainArray.length - 3]

      if (
        penultimate.length === 2 &&
        domainArray.length === 3 &&
        antepenultimate.length > 2 &&
        antepenultimate !== 'www'
      ) {
        log('isMultiPartDomain', logMsg, 'YES', 'TRACE')

        return true
      }
    }

    /* There are other rare or international domains that may have longer penultimates,
     * but there's no point in trying to capture all of the thousands of possibilities */

    log('isMultiPartDomain', logMsg, 'NO', 'TRACE')

    return false
  },

  /**
   * Checks whether one domain is more general than another
   *
   * @param domainToCheck The domain to check
   * @param domainToCompare The domain to compare against
   */
  isMoreGeneralURL: function (
    domainToCheck?: string,
    domainToCompare?: string,
  ) {
    /* Make comparison case-insensitive */
    domainToCheck =
      typeof domainToCheck === 'string'
        ? domainToCheck.toLowerCase()
        : domainToCheck
    domainToCompare =
      typeof domainToCompare === 'string'
        ? domainToCompare.toLowerCase()
        : domainToCompare

    const logMsg =
      'Is ' + domainToCheck + ' more general than ' + domainToCompare + '?'

    if (
      domainToCheck == 'chrome-extension://' &&
      domainToCompare?.indexOf(domainToCheck) == 0
    )
      return true

    domainToCheck = this.stripProtocol(domainToCheck)
    domainToCompare = this.stripProtocol(domainToCompare)

    if (domainToCheck == '' && domainToCompare != '') return false

    const baseDomainToCompare = this.extractBaseDomain(domainToCompare)

    if (domainToCheck === domainToCompare) {
      log('isMoreGeneralURL', logMsg, 'YES', 'TRACE')

      return true
    }

    if (domainToCheck === baseDomainToCompare) {
      log('isMoreGeneralURL', logMsg, 'YES', 'TRACE')

      return true
    }

    /* For example, if the domainToCheck is stayfocusd.com, and the domainToCompare
     * is stayfocusd.com/foo, then the indexOf domainToCheck would be 0 */
    if (domainToCompare.indexOf(domainToCheck) === 0) {
      log('isMoreGeneralURL', logMsg, 'YES', 'TRACE')

      return true
    }

    const baseDomainToCheck = this.extractBaseDomain(domainToCheck)

    /**
     * If both domains are from the same base domain, and the domain to check is
     * shorter than, and is a substring of, the domain to compare, and the domain
     * to check contains either a path or query params, then the domain to check
     * is more general.
     *
     * For example:
     *
     * domainToCheck                    = google.com/reader
     * domainToCompare                  = www.google.com/reader/view/#stream/feed%2Fhttp%3A%2F%2Ffeedproxy.google.com%2FTechcrunch
     *
     * Base domains are the same?                           Yes.
     * domainToCheck is shorter?                            Yes.
     * domainToCheck is substring of domainToCompare?       Yes.
     * domainToCheck has a slash or question mark?          Yes.
     *
     * Therefore, domainToCheck is more general.
     */
    if (
      baseDomainToCheck === baseDomainToCompare &&
      domainToCheck.length <= domainToCompare.length &&
      domainToCompare.indexOf(domainToCheck) >= 0 &&
      (domainToCompare.indexOf('/') >= 0 || domainToCompare.indexOf('?') >= 0)
    ) {
      log('isMoreGeneralURL', logMsg, 'YES', 'TRACE')

      return true
    }

    /**
     * Check if the base domain to check is a substring of the domain to compare,
     * i.e. google.com is a substring of google.com.hk
     */
    if (
      baseDomainToCompare.length > baseDomainToCheck.length &&
      baseDomainToCompare.indexOf(domainToCheck) > -1
    ) {
      log('isMoreGeneralURL', logMsg, 'YES', 'TRACE')

      return true
    }

    log('isMoreGeneralURL', logMsg, 'NO', 'TRACE')

    return false
  },

  /**
   * Checks whether a domain matches a wildcard rule
   *
   * @param wildcard The wildcard rule to match
   * @param domainToCompare The domain to compare to the wildcard rule
   */
  matchesWildcard: function (wildcard: string, domainToCompare: string) {
    if (wildcard.indexOf('*') == -1) return
    const logMsg =
      'Does ' + domainToCompare + ' match wildcard ' + wildcard + '?'

    wildcard = wildcard.toLowerCase()
    domainToCompare = domainToCompare.toLowerCase()

    const rawWildcardString = wildcard.replace('*', '')
    domainToCompare = domainToCompare || ''

    if (domainToCompare.indexOf(rawWildcardString) === -1) {
      log('matchesWildcard', logMsg, 'NO', 'TRACE')

      return false
    }

    const domainUpToWildcard = domainToCompare.substring(
      0,
      domainToCompare.indexOf(rawWildcardString) + rawWildcardString.length,
    )
    const domainSubstring = domainToCompare.substring(
      domainUpToWildcard.length - rawWildcardString.length,
      domainUpToWildcard.length,
    )
    const result = domainSubstring === rawWildcardString

    log('matchesWildcard', logMsg, result ? 'YES' : 'NO', 'TRACE')

    return result
  },

  /**
   * Strips http:// or https:// from url
   *
   * @param url The url to strip
   */
  stripProtocol: function (url?: string) {
    if (url) {
      return url.indexOf('://') > -1
        ? url.slice(url.indexOf('://') + 3, url.length)
        : url
    } else {
      return ''
    }
  },
}

export default DomainParser
