import { API } from './API'

declare module '~/core/API' {
  interface API {
    Requirements: ReturnType<typeof Requirements.initAPI>
  }
}

let api: API

const Requirements = {
  /** @type {Promise<void> | null} */
  initialized: null as null | Promise<void>,

  onboardingSampleGroup: null,

  initAPI(theAPI: API) {
    api = theAPI

    return api.mixin('Requirements', {
      init: this.init.bind(this),
      isOnboardingCompleted: this.isOnboardingCompleted.bind(this),
      getPromptAgainAt: this.getPromptAgainAt.bind(this),
      setPromptAgainAt: this.setPromptAgainAt.bind(this),
      getOnboardingShownCount: this.getOnboardingShownCount.bind(this),
      incrementOnboardingShownCount:
        this.incrementOnboardingShownCount.bind(this),
      getConfirmationCount: this.getConfirmationCount.bind(this),
      incrementConfirmationCount: this.incrementConfirmationCount.bind(this),
      isUnder18: this.isUnder18.bind(this),
    })
  },

  /**
   * @returns {Promise<void>}
   */
  async init() {
    if (this.initialized != null) return

    const { promise, resolve, reject } = api.Utils.getDecoupledPromise()
    this.initialized = promise

    this.onboardingSampleGroup = await api.Storage.getOrSet(
      'onboardingSampleGroup',
      100 * Math.random(),
    )

    try {
      await Promise.all([api.Settings.initAsync(), api.RemoteConfig.init()])
      resolve()
    } catch (err) {
      reject(err)
    }
  },

  /**
   * Checks if the user needs to complete onboarding. Based off the A/B onboarding enabled
   * percent, and the options selected when the user last performed onboarding.
   * @returns {Promise<boolean>}
   */
  async isOnboardingCompleted() {
    const now = Date.now()

    if (this.initialized == null) {
      throw Error(
        'You must call `API.Requirements.init()` before calling `isOnboardingCompleted()`',
      )
    }
    await this.initialized

    const enabledPercentInt = api.RemoteConfig.get(
      'onboardingEnabledPercentInt2',
    )
    const isGroupEnabled = this.onboardingSampleGroup! < enabledPercentInt
    if (!isGroupEnabled) return true

    const isUnder18 = this.isUnder18()
    if (isUnder18) return true

    const hasDeclinedPP = api.Settings.get('explicitlyDeclinedPrivacyPolicy')
    if (hasDeclinedPP) return true

    const hasAcceptedToS = api.Settings.get('hasAcceptedToS')
    if (!hasAcceptedToS) return false

    const hasAcceptedPP = api.Settings.get('hasAcceptedPrivacyPolicy')
    const promptAgainAt = await this.getPromptAgainAt()
    const confirmationCount = await this.getConfirmationCount()
    return !!hasAcceptedPP || now < promptAgainAt || confirmationCount > 3
  },

  /**
   * Gets the next time the onboarding prompt should be shown if user clicked delayed their
   * decision.
   *
   * @returns {Promise<number>}
   */
  async getPromptAgainAt() {
    const time = await api.Storage.get('showOnboardingAgainAt')
    return time ?? 0
  },

  /**
   * @param {number} time The time in MS since Epoch that the user should be prompted again at.
   * @returns {Promise<void>}
   */
  setPromptAgainAt(time: number) {
    return api.Storage.set({ showOnboardingAgainAt: time })
  },

  /**
   * @returns {Promise<number>} The total number of times the user has seen the onboarding
   */
  async getOnboardingShownCount(): Promise<number> {
    const count = await api.Storage.get('onboardingShownCount')
    return count ?? 0
  },

  /**
   * Adds 1 to the onboarding shown count.
   *
   * @returns {Promise<void>}
   */
  async incrementOnboardingShownCount() {
    const count = await this.getOnboardingShownCount()
    await api.Storage.set({ onboardingShownCount: count + 1 })
  },

  /**
   * @returns {Promise<number>} The total number of times the user has declined the privacy policy confirmation
   */
  async getConfirmationCount() {
    const count = await api.Storage.get('privacyPolicyConfirmationCount')
    return count ?? 0
  },

  /**
   * Adds 1 to the privacy policy confirmation count.
   *
   * @returns {Promise<void>}
   */
  async incrementConfirmationCount() {
    const count = await this.getConfirmationCount()
    await api.Storage.set({ privacyPolicyConfirmationCount: count + 1 })
  },

  /**
   * Returns true when the user is under 18. Returns false when not selected, or has checked "is 18 or older box"
   * @returns {Promise<boolean>}
   */
  isUnder18() {
    const is18OrOlder = api.Settings.get('is18OrOlder')
    if (is18OrOlder) return false

    const age = api.Settings.get('age')
    return age && age < 18
  },

  /**
   * @returns {boolean} True if the current hour is known to be an effective time of day for prompting the user to complete onboarding.
   */
  isEffectiveHourOfDay() {
    const ineffectiveHours = [3, 22, 13, 9, 8, 5, 11, 2, 12, 14, 4, 1]
    const currentHour = new Date().getHours()
    return !ineffectiveHours.includes(currentHour)
  },
}

export default Requirements
