/**
 * Use Amplitude via the `core/CoreAPI` module. It is registered under the `Analytics` field.
 *
 * 1. Make sure to call `API.Analytics.init()` first. It returns a promise, so you can use `.then`
 *    to run something as soon as it is initialized.
 *
 * 2. Call `API.Analytics.event('EVENT_NAME', { event: "properties" })` to report an event. The
 *    second parameter for event properties is optional.
 *
 * 3. To report events on the DOM, call `API.Analytics.bindHtmlEvents()`. Then you can add
 *    attributes to your HTML to automatically report events, like on click.
 */

import * as PanelAPIUtils from '@sensortower/panel-api-utils'
import UAParser, { IBrowser, IOS } from 'ua-parser-js'
import { API } from './API'
import GeneralUtils from './GeneralUtils'

declare module '~/core/API' {
  interface API {
    Analytics: ReturnType<typeof Amplitude.initAPI>
  }
}

const log = GeneralUtils.createScopedLog('Amplitude')

let api: API

const Amplitude = {
  installIdKey: 'install-id',

  initialized: null as Promise<void> | null,

  installId: null as string | null, // Unique identifier per install
  browser: null as IBrowser | null, // Name of the browser being used
  os: null as IOS | null, // Operating System in use
  version: null as string | null, // Extension's version
  sessionId: null as number | null, // Session start time in MS since epoch
  sampleGroup: null as number | null, // Number from 0-100, used to decide if an event should be sampled.
  enabled: null as boolean | null, // Whether the user has enabled analytics

  /**
   * Sets Amplitude as the provided Analytics API, defining the public fields on the API
   * object.
   */
  initAPI(theAPI: API) {
    api = theAPI

    return api.mixin('Analytics', {
      init: this.init.bind(this),
      event: this.event.bind(this),
      bindHtmlEvents: this.bindHtmlEvents.bind(this),
    })
  },

  /**
   * Initialize the Amplitude API, loading the details.
   */
  async init() {
    if (this.initialized != null) {
      log?.(
        'init',
        'Amplitude already initialized',
        { sessionId: this.sessionId },
        'DEBUG',
      )
    } else {
      const { promise, resolve } = api.Utils.getDecoupledPromise()
      this.initialized = promise as Promise<void>

      const parser = new UAParser(navigator.userAgent)
      this.sessionId = Date.now()
      this.os = parser.getOS()
      this.browser = parser.getBrowser()
      this.version = api.Chrome.Extension.getVersion()
      this.sampleGroup = await api.Storage.getOrSet(
        'analyticsSamplingGroup',
        100 * Math.random(),
      )
      const canUpload = api.Settings.get('hasAcceptedPrivacyPolicy') ?? true
      const isProduction = !!MINIFIED
      this.enabled = canUpload && isProduction

      this.installId = await api.Storage.get(this.installIdKey)
      if (!this.installId) {
        this.installId = PanelAPIUtils.generateInstallId()
        await api.Storage.set({
          [this.installIdKey]: this.installId,
        })
      }

      resolve()
      log?.('init', 'Initialized Amplitude', this, 'INFO')
    }
  },

  /**
   * Send an event to Amplitude.
   *
   * @param {string} eventType The event's type or name.
   * @param {Record<string, any>} eventProperties Additional properties that should be listed on the event.
   */
  async event(
    eventType: string,
    eventProperties?: Record<string, any>,
  ): Promise<void> {
    if (!this.initialized) {
      log?.('event', 'Cannot report event until initialized', this, 'WARN')
      return
    }

    const timestamp = Date.now()
    await this.initialized
    const amplitudeSampling = api.RemoteConfig.get('amplitudeSampling')
    const defaultSampling = api.RemoteConfig.get('defaultAmplitudeSampling')
    const samplePercentInt = amplitudeSampling[eventType] ?? defaultSampling
    const isSampled = this.sampleGroup! < samplePercentInt
    const enabled = this.enabled && isSampled

    const event = {
      event_type: eventType,
      user_id: this.installId,
      user_properties: {
        hasAcceptedToS: !!api.Settings.get('hasAcceptedToS'),
        hasAcceptedPrivacyPolicy: !!api.Settings.get(
          'hasAcceptedPrivacyPolicy',
        ),
        explicitlyDeclinedPrivacyPolicy: !!api.Settings.get(
          'explicitlyDeclinedPrivacyPolicy',
        ),
        isNewUser: !!api.Settings.get('isNewUser'),
        is18OrOlder: api.Settings.get('is18OrOlder'),
        age: api.Settings.get('age'),
      },
      time: timestamp,
      event_properties: {
        page: `${location.protocol}//${location.hostname}${location.pathname}`,
        ...eventProperties,
      },
      app_version: this.version,
      // Amplitude wants the browser for the os_name, so we pass the os to the
      // platform field instead.
      os_name: this.browser!.name,
      os_version: this.browser!.version,
      platform: this.os!.name,
    }
    log?.(
      'event',
      enabled ? 'Reported event' : 'Skipped event',
      { event, samplePercentInt, isSampled, thisEnabled: this.enabled },
      'DEBUG',
    )
    if (!enabled) return

    await api.AmplitudeAPI.uploadEvents(event)
  },

  /**
   * Queries all elements matching `*[data-analytics-click]` and adds events to their
   * click handlers.
   *
   * For example, if you want to submit the `SIGN_UP` event when clicking on a sign up
   * buttons, add the attribute the the element:
   *
   * ```html
   * <button data-analytics-click="SIGN_UP">Sign Up</button>
   * ```
   *
   * Then call `API.Analytics.bindHtmlEvents()`.
   */
  bindHtmlEvents() {
    log?.(
      'bindHtmlEvents',
      'Binding amplitude events to DOM',
      undefined,
      'DEBUG',
    )

    const clickAttr = 'data-analytics-click'
    document.querySelectorAll(`*[${clickAttr}]`).forEach((element) => {
      element.addEventListener('click', (event) => {
        const target = event.target as HTMLElement
        const eventType = target?.getAttribute?.(clickAttr)
        if (!eventType) return

        this.event(eventType!)
      })
    })

    const toggledAttr = 'data-analytics-toggle'
    document.querySelectorAll(`*[${toggledAttr}]`).forEach((element) => {
      element.addEventListener('change', (event) => {
        const target = event.target as HTMLInputElement
        const checked = target?.checked
        const eventType = target?.getAttribute?.(toggledAttr)
        if (!eventType) return

        this.event(eventType, { checked })
      })
    })
  },
}

export default Amplitude
