import API from '~/core/CoreAPI'
/**
 * Gets the blocklist for use with `isHostnameBlocked`.
 * @returns {Promise<string[]>}
 */
export function getBlocklist() {
  const blockedGroups = API.BlockedGroupsRepository.get()
  let array: string[] = []
  blockedGroups.forEach((group) => {
    array = array.concat(group.websites)
  })
  return array
}

/**
 * Gets the allowlist for use with `isHostnameBlocked`.
 * @returns {Promise<string[]>}
 */
export function getAllowlist() {
  return API.WhitelistRepository.getAll()
}

/**
 * Give a hostname, blocklist, and allowlist, determine if the hostname should be blocked. Check
 * the blocklist first, then uses the allowlist to override.
 * @param {string} hostname
 * @param {string[]} blocklist
 * @param {string[]} allowlist
 * @returns {boolean}
 */

export function isHostnameBlocked(
  hostname: string,
  blocklist: string[],
  allowlist: string[],
) {
  const matchesBlocklist =
    blocklist == null
      ? true
      : blocklist.some((wildcard) =>
          isHostnameBlockedPredicate(wildcard, hostname),
        )
  const matchesAllowlist = allowlist.some((wildcard) =>
    isHostnameBlockedPredicate(wildcard, hostname),
  )

  return matchesBlocklist && !matchesAllowlist
}

export function isHostnameBlockedPredicate(
  domainToCheck: string,
  domainToCompare: string,
) {
  return (
    API.Domain.isMoreGeneralURL(domainToCheck, domainToCompare) ||
    API.Domain.matchesWildcard(domainToCheck, domainToCompare)
  )
}
