import { API } from './API'
import GeneralUtils from './GeneralUtils'
import { DAY } from './Time'

declare module '~/core/API' {
  interface API {
    RemoteConfig: ReturnType<typeof RemoteConfig.initAPI>
  }
}

const log = GeneralUtils.createScopedLog('RemoteConfig')

let api: API

interface RemoteConfigDefaults {
  onboardingEnabledPercentInt2: number
  bugsnagSampling: number
  pageViewAllowlist: any[]
  bugsnagIgnoreMatches: string[]
  usageUploadBatchSize: number
  newTabTimeout: number
  uploadWebUsage: boolean
  defaultAmplitudeSampling: number
  amplitudeSampling: {}
  incrementalRollout: {}
  instacartSelectors: {}
  forbiddenPromptWords: string[]
  chatGptUrl: string
  chatGptConversationUrl: string
}

const RemoteConfig = {
  cache: {} as Record<string, any>,
  initialized: false,
  defaults: {
    onboardingEnabledPercentInt2: 0,
    defaultAmplitudeSampling: 0.1, // 0-100  0=disabled  100=fully enabled
    amplitudeSampling: {}, // 0-100  0=disabled  100=fully enabled
    newTabTimeout: 3 * DAY,
    uploadWebUsage: true,
    usageUploadBatchSize: 500,
    pageViewAllowlist: [],
    incrementalRollout: {},
    bugsnagSampling: 0.01, // 0-100  0=disabled  100=fully enabled
    bugsnagIgnoreMatches: [
      'Extension context invalidated.',
      'Could not establish connection. Receiving end does not exist.',
      'The browser is shutting down.',
      '{\\s*"?isTrusted"?:\\s*true\\s*}',
      "Failed to execute 'abort' on 'IDBTransaction': The transaction has finished.",
      "Failed to execute 'transaction' on 'IDBDatabase': The database connection is closing.",
      '^IO error: \\.\\.\\.\\/.+?: .*? \\(ChromeMethodBFE: .*\\)$',
      'Cannot access contents of the page. Extension manifest must request permission to access the respective host.',
      'The extensions gallery cannot be scripted.',
    ], // Regexes to ignore in Bugsnag – ignore errors that are not actionable
    instacartSelectors: {
      productSelector: ['div[aria-label="Product"]'],
      sponsoredRegex: ['/s\\s*p\\s*o\\s*n\\s*s\\s*o\\s*r\\s*/i'],
      companyName: ['.e-1f1gsmn', '.e-1opt332'],
      searchTerm: ['#search-bar-input'],
      productTitle: ['.e-th3ch3', '.e-u2e78b', '.e-1exr31r', '.e-wrmemy'],
      productSize: ['.e-k9ly30', '.e-owckh2', '.e-1hb8rq9', '.e-1188c9v'],
      productPrice: [
        '.e-1jioxed > span.screen-reader-only',
        '.e-1ybpdrh > span.screen-reader-only',
        '.e-1ghw7u1 > span.screen-reader-only',
        '.e-hlbpyw > span.screen-reader-only',
        '.e-t5lw1y > span.e-13d1dsr',
      ],
    },
    forbiddenPromptWords: ['drug', 'alcohol', 'porn'],
    chatGptUrl: 'https://chatgpt.com/',
    chatGptConversationUrl: 'https://chatgpt.com/c/',
  } as RemoteConfigDefaults,
  url: `https://stayfocusd.st-panel-api.com/v1/remote_config/stayfocusd${
    MINIFIED ? '' : '-dev'
  }`,
  sharedConfigUrl:
    'https://stayfocusd.st-panel-api.com/v1/remote_config/shared-web-config',
  storageKey: 'remote-config',

  /**
   * Initializes RemoteConfig API
   *
   * @param theAPI
   */
  initAPI(theAPI: API) {
    api = theAPI

    return api.mixin('RemoteConfig', {
      storageKey: this.storageKey,
      init: this.init.bind(this),
      get: this.get.bind(this),
      refresh: this.refresh.bind(this),
    })
  },

  /**
   * Restart the remote config from storage and refresh to grab the latest config.
   */
  async init() {
    if (this.initialized) {
      log?.('init', 'RemoteConfig already initialized', this.cache, 'DEBUG')
      return
    }

    log?.('init', 'Initializing RemoteConfig', this.defaults, 'DEBUG')

    this.initialized = true
    this.cache = { ...this.defaults }
    this.cache = {
      ...this.cache,
      ...(await api.Storage.get(this.storageKey)),
    }

    log?.('init', 'Initialized', this.cache, 'DEBUG')
  },

  /**
   * Refreshes remote config from the remote API
   */
  async refresh() {
    log?.('refresh', 'Refreshing...', { currentConfig: this.cache }, 'DEBUG')

    try {
      const latestConfig = await fetch(this.url).then((res) => res.json())
      const latestSharedConfig = await fetch(this.sharedConfigUrl).then((res) =>
        res.json(),
      )
      log?.('refresh', 'Fetched config', latestConfig, 'DEBUG')

      this.cache = {
        ...this.defaults,
        ...latestConfig,
        ...latestSharedConfig,
      }
      await api.Storage.set({ [this.storageKey]: this.cache })
      log?.('refresh', 'Saved config', this.cache, 'DEBUG')
    } catch (err) {
      log?.('refresh', 'Failed to fetch remote config', err, 'WARN')
    }
  },

  /**
   * Retrieves a field from remote config value
   *
   * @param {string} key The field to retrieve
   * @returns {any}
   */
  get(key: string) {
    if (!this.inCache(key)) {
      throw new Error(
        '[RemoteConfig.get()] ' +
          key +
          ' is not registered as a key in RemoteConfig.js',
      )
    }
    return this.cache[key]
  },

  /**
   * Checks whether a field is in the cache
   *
   * @param key The field to check
   * @returns {boolean}
   */
  inCache(key: string) {
    return typeof this.cache[key] !== 'undefined'
  },
}

export default RemoteConfig
