import { API } from './API'
import Amplitude from './Amplitude'
import AmplitudeAPI from './AmplitudeAPI'
import ChromeAPI from './ChromeAPI'
import DateUtils from './DateUtils'
import DomainParser from './DomainParser'
import GeneralUtils from './GeneralUtils'
import IncrementalRollout from './IncrementalRollout'
import ObjectUtils from './ObjectUtils'
import Requirements from './Requirements'
import Storage from './Storage'
import Settings from './Settings'
import RemoteConfig from './RemoteConfig'
import WebUsage from './WebUsage'
import ScheduleJobs from './ScheduleJobs'
import PubSub from 'dropletjs.pubsub'
import Notification from './Notification'
import NuclearOption from './NuclearOption'
import BlockedGroupsTimer from './BlockedGroupsTimer'
import BlockedAllWebsites from './BlockedAllWebsites'
import BlockedGroupsRepository from './BlockedGroupsRepository'
import StayFocusd from './StayFocusd'
import Bugsnag from './Bugsnag'
import WhitelistRepository from './WhitelistRepository'

const classes: any[] = [
  API,
  Amplitude,
  AmplitudeAPI,
  Bugsnag,
  ChromeAPI,
  DateUtils,
  DomainParser,
  GeneralUtils,
  IncrementalRollout,
  ObjectUtils,
  Requirements,
  Storage,
  Settings,
  RemoteConfig,
  WebUsage,
  ScheduleJobs,
  PubSub,
  Notification,
  NuclearOption,
  BlockedGroupsRepository,
  BlockedAllWebsites,
  BlockedGroupsTimer,
  WhitelistRepository,
  StayFocusd,
]

/* Add third-party libraries to API */
declare module '~/core/API' {
  interface API {
    PubSub: typeof PubSub
  }
}
API.mixin({
  mixin: API.mixin.bind(API),
  PubSub,
})

/*
 * Call initAPI() method on each of the required classes. This is how each class
 * exposes its methods to the API
 */
for (let i = 0; i < classes.length; i++) {
  const thisClass = classes[i]

  if (
    typeof thisClass === 'object' &&
    thisClass !== null &&
    typeof thisClass.initAPI === 'function'
  ) {
    thisClass.initAPI(API)
  }
}

/*
 * If the background page is available and the API has already been defined
 * there, use that. This ensures that all references to the API are consistent
 * the across background script, options page, and popup
 */

/**
 * Don't use the background page for some APIs, like Analytics.
 */
Amplitude.initAPI(API) // Must run in the current context so the sessionId is generated per page
Bugsnag.initAPI(API) // Must run in the current context so it reports errors from each page

export default API
