import { API } from './API'
import GeneralUtils from './GeneralUtils'

declare module '~/core/API' {
  interface API {
    BlockedGroupsTimer: ReturnType<typeof BlockedGroupsTimer.initAPI>
  }
}

/**
 * @type {((funcName: string, message: string, payload: any, level: 'ERROR' | 'WARN' | 'INFO' | 'DEBUG') => void) | undefined}
 */
const log = GeneralUtils.createScopedLog('WebUsage')

const TICK_INTERVAL = 1000

let api: API

const BlockedGroupsTimer = {
  _timer: null as NodeJS.Timer | null,
  _isNewDay: null as Function | null,
  _killPageAction: null as Function | null,
  _updateBadgeAction: null as Function | null,
  _postTimerUpdatedMessage: null as Function | null,
  _showNotification: null as Function | null,

  initAPI(theAPI: API) {
    api = theAPI

    return api.mixin('BlockedGroupsTimer', {
      init: this.init.bind(this),
      startCountdown: this.startCountdown.bind(this),
      stopCountdown: this.stopCountdown.bind(this),
      tick: this.tick.bind(this),
      isMaxTimeAllowedExceeded: this.isMaxTimeAllowedExceeded.bind(this),
      getTotalSecondsRemaining: this.getTotalSecondsRemaining.bind(this),
    })
  },

  async init(
    isNewDay: () => boolean,
    killPageAction: (url: string) => boolean,
    updateBadgeAction: (totalSecondsRemaining: number) => void,
    postTimerUpdatedMessage: () => void,
    showNotification: (totalSecondsRemaining: number) => void,
  ): Promise<void> {
    this._isNewDay = isNewDay
    this._killPageAction = killPageAction
    this._updateBadgeAction = updateBadgeAction
    this._postTimerUpdatedMessage = postTimerUpdatedMessage
    this._showNotification = showNotification
  },

  /**
   * Starts countdown timer
   */
  startCountdown(url: string) {
    if (this._timer) {
      this.stopCountdown()
    }
    if (!api.Settings.get('activated', true)) {
      log('startCountdown', 'Extension is not activated')
      return
    }
    log('startCountdown', 'Starting countdown')
    this._timer = setInterval(async () => await this.tick(url), TICK_INTERVAL)
  },

  /**
   * Stops countdown timer
   */
  stopCountdown() {
    log('stopCountdown', 'Stopping countdown')
    if (this._timer) clearInterval(this._timer as unknown as number)
    this._timer = null
  },

  /**
   * Increments elapsedTime counter in local storage. If time spent
   * exceeds the maximum allowed, kills page
   */
  async tick(url: string) {
    if (this._isNewDay?.()) {
      api.BlockedGroupsRepository.clearElapsedTime()
    }

    const blockTimeForAllObj = api.BlockedAllWebsites.getTime()
    if (blockTimeForAllObj != null) {
      await api.Storage.set({
        blockAllWebsitesElapsedTime: blockTimeForAllObj.elapsedTime + 1,
      })

      this.actionsAfterCheckingTime(
        url,
        blockTimeForAllObj.elapsedTime,
        blockTimeForAllObj.allowedTime - blockTimeForAllObj.elapsedTime,
      )
    } else {
      const group = api.BlockedGroupsRepository.getBlockedGroupByDomain(url)
      if (!group) return

      await api.BlockedGroupsRepository.incrementElapsedTime(group.groupName)

      const totalSecondsRemainingForGroup =
        group.maxTimeAllowed - group.elapsedTime

      this.actionsAfterCheckingTime(
        url,
        group.elapsedTime,
        totalSecondsRemainingForGroup,
      )
    }
  },

  actionsAfterCheckingTime(
    url: string,
    elapsedTime: number,
    remainingTime: number,
  ) {
    log(
      'tick',
      `Elapsed time: ${elapsedTime}. Time remaining: ${remainingTime}`,
      null,
      'DEBUG',
    )

    this._showNotification?.(remainingTime)

    if (remainingTime <= 0) {
      this._killPageAction?.(url)
      return
    }

    this._postTimerUpdatedMessage?.()
    this._updateBadgeAction?.(remainingTime)
  },

  isMaxTimeAllowedExceeded(url: string) {
    const isMaxTimeAllowedExceededForAll =
      api.BlockedAllWebsites.isMaxTimeAllowedExceeded()
    if (isMaxTimeAllowedExceededForAll != null) {
      return isMaxTimeAllowedExceededForAll
    } else {
      const group = api.BlockedGroupsRepository.getBlockedGroupByDomain(url)
      if (group) {
        return group.elapsedTime >= group.maxTimeAllowed
      }
    }

    return false
  },

  getTotalSecondsRemaining(url: string) {
    const getTotalSecondsRemainingForAll =
      api.BlockedAllWebsites.getTotalSecondsRemaining()
    if (getTotalSecondsRemainingForAll != null) {
      return getTotalSecondsRemainingForAll
    } else {
      const group = api.BlockedGroupsRepository.getBlockedGroupByDomain(url)
      if (group != null) {
        return group.maxTimeAllowed - group.elapsedTime
      }
    }
  },
}
export default BlockedGroupsTimer
