import DomainParser from './DomainParser'
import NuclearOption from './NuclearOption'
import Notification from './Notification'
import Message from './Message'
import Time, { MINUTE, SECOND } from './Time'
import { API } from './API'
import GeneralUtils from './GeneralUtils'
import Utils from './GeneralUtils'
import { BadgeColor, BadgeIcon, badgeIcons } from './badge-icons'
import Browser from 'webextension-polyfill'

declare module '~/core/API' {
  interface API {
    StayFocusd: ReturnType<typeof StayFocusd.initAPI>
  }
}

const log = GeneralUtils.createScopedLog('StayFocusd')
const changeBadgeIcons = badgeIcons()

let api: API

/**
 * Primary javascript logic for StayFocusd extension
 *
 * @author              Warren Benedetto <wmbenedetto@gmail.com>
 * @since               January 14, 2010
 */
interface ActiveHours {
  endHour: string
  endMin: string
  startHourInt: number
  startMinInt: number
  startHour: string
  startMin: string
  endHourInt: number
  endMinInt: number
  startTime: string
  endTime: string
}

export interface ActiveHoursQueue {
  timestamp: number
  startTime: string
  endTime: string
}

export interface ResetTimeQueue {
  timestamp: string
  resetTime: string
}

export interface DisplayTimerResult {
  groupName: string | null
  time: string
  nuclear: boolean
}

const onboardingOpenedAtStorageKey = 'onboarding-opened-at'

const StayFocusd = {
  firstInstallAllowance: 60, // in minutes
  interval: 1, // how often to check elapsed time, in seconds
  timer: null, // holds setInterval() timer value
  apiURL: 'https://www.stayfocusd.com',
  redirectURL: 'https://www.stayfocusd.com/blocked/',
  uninstallUrl: 'https://www.stayfocusd.com/goodbye/',
  onboardingUrl: 'https://www.stayfocusd.com/extension-get-started',
  currentURL: null as string | null,
  version: null as string | null,
  selectedTabID: null as number | null,
  active: null,
  popupPort: null as null | chrome.runtime.Port,

  /**
   * Initializes API
   *
   * @param theAPI
   */
  initAPI: function (theAPI: API) {
    api = theAPI
    //window.API = api

    return api.mixin('StayFocusd', {
      getActiveDays: this.getActiveDays.bind(this),
      getActiveHours: this.getActiveHours.bind(this),
      getActiveHoursQueue: this.getActiveHoursQueue.bind(this),
      getAPIURL: () => this.apiURL,
      getCurrentURL: () => this.currentURL,
      getResetTime: this.getResetTime.bind(this),
      getResetTimeQueue: this.getResetTimeQueue.bind(this),
      getTotalSecondsRemaining: this.getTotalSecondsRemaining.bind(this),
      isActive: this.isActive.bind(this),
      isFirstInstallAllowanceActive:
        this.isFirstInstallAllowanceActive.bind(this),
      isUpdatePopupDisabled: this.isUpdatePopupDisabled.bind(this),
      localizeHTML: this.localizeHTML.bind(this),
      setActiveDays: this.setActiveDays.bind(this),
      setActiveHoursQueue: this.setActiveHoursQueue.bind(this),
      setResetTimeQueue: this.setResetTimeQueue.bind(this),
      updateBadge: this.updateBadge.bind(this),
      hideBadge: this.hideBadge.bind(this),
      postTimerUpdatedMessage: this.postTimerUpdatedMessage.bind(this),
      onInstalled: this.onInstalled.bind(this),
      onUpdated: this.onUpdated.bind(this),
      activated: this.activated.bind(this),
      setActivated: this.setActivated.bind(this),
      isActivateSwitchShown: this.isActivateSwitchShown.bind(this),
      setActivateSwitchShown: this.setActivateSwitchShown.bind(this),
    })
  },

  /**
   * Initializes extension
   */
  async init() {
    log('init', 'Initializing StayFocusd', null, 'DEBUG')

    this.initCheckingTab()
    await this.initSettings()
    await this.initRemoteConfig()
    this.initAnalytics()
    this.initBugsnag()
    await this.initRequirements()
    await this.initBlockedGroups()
    this.initBlockedGroupTimer()
    await this.initWhitelist()
    await this.initNuclearOption()
    await this.initNotification()
    this.initListeners()
    this.initUninstallURL()
    await this.initWebUsageCollect()
    this.initScheduleJobs()
    await this.forceOnboardingOnNewTab()
  },

  initBlockedGroupTimer() {
    const isNewDay = () => this.isNewDay()
    const killPageAction = (url: string) => this.killPage(url)
    const updateBadgeAction = (totalSecondsRemaining: number) =>
      this.updateBadge(totalSecondsRemaining)
    const postTimerUpdatedMessage = () => this.postTimerUpdatedMessage()
    const showNotification = (totalSecondsRemaining: number) =>
      this.showNotification(totalSecondsRemaining)

    api.BlockedGroupsTimer.init(
      isNewDay,
      killPageAction,
      updateBadgeAction,
      postTimerUpdatedMessage,
      showNotification,
    )
  },

  /**
   * Initializes settings
   */
  async initSettings() {
    await api.Settings.initAsync()

    await api.Storage.setBucket([
      'backupKeys',
      'disableUpdatePopup',
      'isUpdated',
      'previousVersion',
      'productivityBypass',
    ])

    /* When storage data changes, refresh Settings */
    api.Storage.addOnChangeListener(
      async (changes: Record<string, any>, areaName: string) => {
        log('init', areaName + ' storage changed', changes)

        api.Settings.refresh(() => {
          api.PubSub.publish('Settings.data.refreshed', {
            areaName: areaName,
            changes: changes,
          })
        })
      },
    )

    if (this.isFirstInstall()) {
      this.setFirstInstallAllowance()
    }
  },

  /**
   * Initialize the remote config module.
   */
  async initRemoteConfig() {
    await api.RemoteConfig.init()

    // Set up an Alarm to refresh remote config on an interval
    await api.Chrome.Alarms.ensure('remote-config', { periodInMinutes: 30 })
    api.Chrome.Alarms.addListener(
      'onAlarm',
      async (alarm: chrome.alarms.Alarm) => {
        if (alarm.name !== 'remote-config') return
        await api.RemoteConfig.refresh()
      },
    )
    await api.RemoteConfig.refresh()
  },

  initAnalytics() {
    api.Analytics.init()
  },

  initBugsnag() {
    api.Bugsnag.init('background')
  },

  async initWebUsageCollect() {
    await api.WebUsage.init()

    chrome.runtime.onConnect.addListener((port) => {
      if (port.name == 'getUsageSummaryData') {
        port.onMessage.addListener(async (msg) => {
          if (msg.message == 'usage.getUsageSummaryData.request') {
            const usageDatabase = await api.WebUsage.getUsageDatabase()
            const sessions = await usageDatabase.sessionsRepo.findSessions(
              msg.startDate,
              msg.endDate,
            )
            const ads = await usageDatabase.adsRepo.findAds(
              msg.startDate,
              msg.endDate,
            )
            port.postMessage({
              message: 'usage.getUsageSummaryData.response',
              startDate: msg.startDate,
              endDate: msg.endDate,
              sessions: sessions,
              ads: ads,
            })
          }
        })
      }
    })
  },

  initCheckingTab() {
    setInterval(() => {
      api.Chrome.Tab.getSelected(undefined, (tab) => {
        if (!tab) return
        api.PubSub.publish('StayFocusd.tab.activity', {
          tabID: tab.id,
          tab,
        })
      })
    }, 1000)
  },

  initScheduleJobs() {
    api.ScheduleJobs.init()
  },

  async initRequirements() {
    await api.Requirements.init()
  },

  /**
   * Initializes blocked groups
   */
  async initBlockedGroups() {
    await api.BlockedGroupsRepository.init()
    await api.BlockedGroupsRepository.migrate()

    if (this.isNewDay() === true) {
      api.BlockedGroupsRepository.clearElapsedTime()
    }
  },

  /**
   * Initializes whitelist
   */
  async initWhitelist() {
    log('initWhitelist', 'Initializing whitelist', null, 'DEBUG')
    await api.WhitelistRepository.init()
    await api.WhitelistRepository.migrate()
  },

  /**
   * Initializes Nuclear Option
   */
  async initNuclearOption() {
    log('initNuclearOption', 'Initializing Nuclear Option', null, 'DEBUG')

    await NuclearOption.initAsync()
    log('initNuclearOption', 'Nuclear Option initialized', null, 'DEBUG')
  },

  /**
   * Initializes Notification
   */
  async initNotification() {
    log('initNotification', 'Initializing Notification', null, 'DEBUG')

    await Notification.initAsync()

    log('initNotification', 'Notification initialized', null, 'DEBUG')
  },

  showNotification(totalSecondsRemaining: number) {
    if (Notification.isset(totalSecondsRemaining)) {
      Notification.show('block')
    }
  },

  /**
   * Initializes listeners
   */
  initListeners() {
    log('initListeners', 'Initializing listeners', null, 'DEBUG')

    /* Subscribe to all messages and pipe to tabs through the Chrome.Message API */
    api.PubSub.subscribe({
      async: true,
      handler: async (
        message: { msgString: string; msgArray: string[]; namespace: string },
        payload: any,
        callback: (response: any) => void | boolean,
      ) => {
        function callbackWrapper(
          innerCallback: (response: any) => void | boolean,
        ) {
          if (chrome.runtime.lastError) {
            log('initListeners', 'runtime.lastError', chrome.runtime.lastError)
          }

          if (
            innerCallback != undefined &&
            typeof innerCallback === 'function'
          ) {
            return innerCallback
          } else {
            return () => true
          }
        }

        callback = callbackWrapper(callback)

        /* If the message isn't coming from a tab, send it */

        if (!payload) return true
        if (!payload.tabID) return true
        if (payload.sender?.tab) return true

        const tab = await api.Chrome.Tab.get(payload.tabID)

        // Don't send a message to a non-existent tab
        if (!tab) return true

        // Don't send a message to a loading tab because content script won't be loaded yet
        if (tab.status === 'loading') return true

        const url = (tab.url == '' ? tab.pendingUrl : tab.url) ?? ''

        // Don't send a message to tabs in which content script cannot be loaded
        if (/^about:|^chrome-extension:|^chrome:\/\//i.test(url)) return true

        api.Chrome.Message.sendToTab(
          payload.tabID,
          { message: message.toString(), payload: payload },
          callback,
        )

        return true
      },
    })

    /* Listen for requests coming from content scripts and republish via PubSub */
    api.Chrome.Message.on.addListener(
      (
        request: Record<string, any>,
        sender: Record<string, any>,
        callback?: Function,
      ) => {
        request.payload = request.payload || {}
        request.payload.sender = sender || {}

        // if the message is coming from proxy-service, don't interfere with it
        if (/^proxy-service.*$/.test(request.type)) {
          return true
        }

        /* If the message is coming from a tab, republish it */
        if (sender.tab) {
          api.PubSub.publish({
            message: request.message,
            payload: request.payload,
            onPublish: typeof callback === 'function' ? callback : () => {},
          })
        }

        return true
      },
    )

    // FIXME: Hack to detect whether tab is blockable when ActivityMonitor loads
    api.PubSub.listen('ActivityMonitor.component.initialized', () => {
      api.Chrome.Tab.getSelected(undefined, (tab) => {
        if (!tab) return

        api.PubSub.publish('StayFocusd.tab.selected', {
          tab,
          tabID: tab.id,
          blockable: this.isBlockable(tab.url ?? ''),
        })
      })
    })

    api.PubSub.listen({
      message: 'ActivityMonitor.overlay.hidden', // TODO: Prefer not to know about ActivityMonitor
      async: true,

      handler: (
        message: Record<string, any>,
        payload: Record<string, any>,
        callback?: Function,
      ) => {
        const response = {
          countdownStarted: api.BlockedGroupsTimer.startCountdown(
            this.currentURL!,
          ),
        }

        if (typeof callback === 'function') {
          callback(response)
        }
      },
    })

    api.PubSub.listen({
      message: 'ActivityMonitor.overlay.shown', // TODO: Prefer not to know about ActivityMonitor
      async: true,

      handler: (
        message: Record<string, any>,
        payload: Record<string, any>,
        callback?: Function,
      ) => {
        const response = {
          countdownStopped: api.BlockedGroupsTimer.stopCountdown(),
        }

        if (typeof callback === 'function') {
          callback(response)
        }
      },
    })

    /* Add listener for when connection from popup is established */
    api.Chrome.Extension.addListener(
      'onConnect',
      (port: chrome.runtime.Port) => {
        if (port.name === 'popup') {
          this.onPopupConnected(port)
        }
      },
    )

    api.Chrome.Tab.getSelected(undefined, async (tab) => {
      await this.onTabStateChange(tab)
    })

    /* Add listener to check domain each time new tab is selected */
    api.Chrome.Tab.addListener('onActivated', () => {
      api.Chrome.Tab.getSelected(undefined, async (tab) => {
        await this.onTabStateChange(tab)
      })
    })

    /* Add listener to check domain each time tab is updated */
    api.Chrome.Tab.addListener('onUpdated', () => {
      api.Chrome.Tab.getSelected(undefined, async (tab) => {
        await this.onTabStateChange(tab)
      })
    })

    /* Add listener to stop timer each time tab is removed */
    api.Chrome.Tab.addListener('onRemoved', () => {
      api.Chrome.Tab.getSelected(undefined, (tab) => {
        /* If the tab is undefined, that means the window is totally closed.
         * Therefore, the timer should be stopped. If there's still another
         * window open and there's a blocked site in a tab there, that will
         * restart the timer. */
        if (typeof tab === 'undefined') {
          api.BlockedGroupsTimer.stopCountdown()
        }
      })
    })

    /* Add listener to stop timer each time window is removed */
    api.Chrome.Window.addListener('onRemoved', () => {
      api.BlockedGroupsTimer.stopCountdown()
    })

    /* Add listener to check domain each time new window is focused */
    api.Chrome.Window.addListener('onFocusChanged', () => {
      api.Chrome.Tab.getSelected(undefined, async (tab) => {
        await this.onTabStateChange(tab)
      })
    })
  },

  async onInstalled(details: chrome.runtime.InstalledDetails) {
    await chrome.tabs.create({ url: this.onboardingUrl, active: true })
    log('onInstalled', 'Installed extension', details, 'INFO')

    const { promise: webOnboardingPromise, resolve: webOnboardingResolve } =
      GeneralUtils.getDecoupledPromise<void>()

    chrome.runtime.onConnect.addListener((port) => {
      if (port.name == 'startOnboardingAfterInstall') {
        port.onMessage.addListener(async (msg) => {
          if (msg.message == 'start-onboarding') {
            webOnboardingResolve()
          }
        })
      }
    })

    await webOnboardingPromise
    api.Settings.set({ isNewUser: true })
    await this.openOnboarding()
  },
  async onUpdated(details: chrome.runtime.InstalledDetails) {
    /* window.onInstalledDetails is set in background.js when the onInstalled listener is called.
     * It's set up this way because the onInstalled listener must be set immediately when
     * the extension loads in order for it to exist when the onInstalled event fires. */
    if (!details.previousVersion) return

    const prevVersion: string = await api.Storage.get('previousVersion')
    const disableUpdatePopup: string | boolean = await api.Storage.get(
      'disableUpdatePopup',
    )
    /**
     * Only pop update tab if details.previousVersion doesn't match
     * the last previousVersion that was recorded on the last update.
     * This is needed because the "update" reason seems to be triggered
     * for some users even when A.) StayFocusd has already updated or
     * B.) there's a Chrome update pending.
     *
     * @see https://code.google.com/p/chromium/issues/detail?id=303481
     */
    if (prevVersion !== details.previousVersion) {
      if (disableUpdatePopup !== 'true' && disableUpdatePopup !== true) {
        changeBadgeIcons.showBadge('NEW', BadgeColor.GREEN)
      }

      api.Storage.set({
        isUpdated: true,
        previousVersion: details.previousVersion,
      })
    }
  },
  async openOnboarding() {
    log('openOnboarding', 'Opening onboarding in new tab', undefined, 'INFO')

    await api.Storage.set({ [onboardingOpenedAtStorageKey]: Date.now() })
    await api.Chrome.Tab.update({
      url: this.getOptionsPage(),
      active: true,
    })
  },
  async forceOnboardingOnNewTab() {
    log('initOnboarding', 'initializing...', undefined, 'INFO')

    const firstInstallDate = await this.getFirstSensorTowerInstallDate()

    api.Chrome.Tab.addListener('onCreated', async (tab: chrome.tabs.Tab) => {
      if (Date.now() - firstInstallDate < MINUTE) return

      log('initOnboarding', 'tab.onCreated', tab, 'DEBUG')

      const newTabTimeout = api.RemoteConfig.get('newTabTimeout')
      const isOnboardingCompleted =
        await api.Requirements.isOnboardingCompleted()
      const onboardingOpenedAt =
        (await api.Storage.get(onboardingOpenedAtStorageKey)) ?? 0
      const canOpenTab = Date.now() >= onboardingOpenedAt + newTabTimeout
      if (!isOnboardingCompleted && canOpenTab) {
        await api.Analytics.event('OPENING_ONBOARDING_ON_TAB_CREATED')
        await this.openOnboarding()
      }
    })
  },

  initUninstallURL() {
    api.Chrome.Extension.setUninstallURL(this.uninstallUrl)
  },

  /**
   * Callback to run each time popup connects to background page
   *
   * @param port The port on which the connection has been made
   */
  onPopupConnected(port: chrome.runtime.Port) {
    log('onPopupConnected', 'Popup connected', port, 'DEBUG')

    this.popupPort = port

    port.onMessage.addListener(async (obj) => {
      const message = new Message(obj.message)

      if (message.matches('*.domain.add.BLACKLIST')) {
        const groupName = obj.payload.groupName
        const domain = obj.payload.domain
        const result = await api.BlockedGroupsRepository.addWebsite(
          groupName,
          domain,
        )

        port.postMessage({
          message: 'StayFocusd.domain.added.' + message.getDescriptor(),
          payload: {
            success: result,
          },
        })

        // check
        await this.checkURL(obj.payload.domain)
      }

      if (message.matches('*.domain.add.WHITELIST')) {
        const domain = [obj.payload.domain]
        const result = await api.WhitelistRepository.add(domain)

        port.postMessage({
          message: 'StayFocusd.domain.added.' + message.getDescriptor(),
          payload: {
            success: result,
          },
        })

        // check
        await this.checkURL(obj.payload.domain)
      }

      if (message.matches('PopupController.toggle.*.*')) {
        const { value } = obj.payload
        this.setActivated(value)
        port.postMessage({
          message: 'StayFocusd.toggled',
          payload: {
            value: this.activated(),
          },
        })

        api.Chrome.Tab.getSelected(undefined, async (tab) => {
          await this.onTabStateChange(tab)
        })
      }

      return true
    })

    port.onDisconnect.addListener(() => {
      this.popupPort = null
    })

    this.postTimerUpdatedMessage()
  },

  /**
   * Updates popup timer
   */
  postTimerUpdatedMessage: function () {
    api.PubSub.publish('StayFocusd.timer.updated', {
      tabID: this.selectedTabID,
      displayTimer: this.getDisplayTimer(),
    })

    if (this.popupPort) {
      this.popupPort.postMessage({
        message: 'StayFocusd.timer.updated',
        payload: {
          displayTimer: this.getDisplayTimer(),
        },
      })
    }
  },

  /**
   * Checks url when state of tab changes (selected, focused, updated)
   *
   * @param tab The tab object for the selected tab
   */
  onTabStateChange: async function (tab: chrome.tabs.Tab | undefined) {
    if (typeof tab === 'undefined') {
      return
    }

    log('onTabStateChange', 'Tab state changed', tab)

    this.currentURL = tab.url!
    this.selectedTabID = tab.id!
    await this.checkURL(tab.url)

    const isBlockable = this.isBlockable(tab.url)

    api.PubSub.publish('StayFocusd.tab.selected', {
      tab: tab,
      tabID: tab.id,
      blockable: isBlockable,
    })
  },

  /**
   * Checks whether the current URL has been blacklisted
   *
   * @param url The url to check
   */
  checkURL: async function (url?: string) {
    url =
      typeof url === 'undefined' ||
      (typeof url === 'string' && url.length === 0)
        ? this.currentURL!
        : url

    log('checkURL', 'Checking url: ', url)

    if (this.isNewDay() === true) {
      api.BlockedGroupsRepository.clearElapsedTime()
    }

    api.BlockedGroupsTimer.stopCountdown()

    if (this.isBlockable(url)) {
      api.Chrome.Tab.getSelected(undefined, (tab) => {
        this.setBadgeIcon(tab, BadgeIcon.BLOCKED)
      })

      if (this.isKillable(url)) {
        this.killPage(url)
      } else {
        api.BlockedGroupsTimer.startCountdown(url)
      }
    } else {
      const isInWhitelist = api.WhitelistRepository.isExists(url)
      if (isInWhitelist) {
        api.Chrome.Tab.getSelected(undefined, (tab) => {
          this.setBadgeIcon(tab, BadgeIcon.ALLOWED)
        })
      } else {
        api.Chrome.Tab.getSelected(undefined, (tab) => {
          this.setBadgeIcon(tab, BadgeIcon.DEFAULT)
        })
      }
    }
  },

  setBadgeIcon(tab: chrome.tabs.Tab | undefined, defaultValue: BadgeIcon) {
    if (tab) {
      if (this.activated()) {
        if (NuclearOption.isActive())
          changeBadgeIcons.setIcon(BadgeIcon.NUCLEAR, tab.id)
        else changeBadgeIcons.setIcon(defaultValue, tab.id)
      } else {
        changeBadgeIcons.setIcon(BadgeIcon.DEACTIVATED, tab.id)
      }
    }
  },

  /**
   * Check whether the page is killable
   */
  isKillable: function (url: string) {
    return (
      api.BlockedGroupsTimer.isMaxTimeAllowedExceeded(url) ||
      NuclearOption.isActive()
    )
  },

  /**
   * Checks whether the domain is a protected domain (and
   * therefore can't be blocked)
   *
   * @param domain The domain to check
   */
  isProtectedURL: function (domain: string) {
    if (
      domain === null ||
      typeof domain === 'undefined' ||
      domain.length === 0
    ) {
      return false
    }

    /* Redirect URL can't be blocked */
    if (domain.indexOf(this.redirectURL) === 0) {
      return true
    }

    /* Never block Rescue Me page */
    if (domain.indexOf('rescueMe') >= 0) {
      return true
    }

    /* Allow access to devtools */
    if (
      domain.indexOf('chrome') >= 0 &&
      domain.indexOf('chrome') < domain.indexOf('://') &&
      (domain.indexOf('sf') > -1 || domain.indexOf('devtools') > -1)
    ) {
      return true
    }

    return false
  },

  /**
   * Checks whether a domain is blockable
   *
   * @param domain The domain to check
   */
  isBlockable: function (domain?: string): boolean {
    if (!this.activated()) return false
    if (this.isProtectedURL(domain!)) {
      return false
    }

    if (domain === null || typeof domain === 'undefined' || domain === '') {
      return false
    }

    if (this.isActive() === false) {
      return false
    }

    const isBlacklisted = api.BlockedGroupsRepository.isBlockable(domain)
    const isWhitelisted = api.WhitelistRepository.isExists(domain)
    const isBlockableByNuclearOption = NuclearOption.isBlockable(
      isBlacklisted,
      isWhitelisted,
    )
    const blockAllWebsites = api.Settings.get('isBlockAllWebsites')

    if (blockAllWebsites && !isWhitelisted) return true

    if (isBlockableByNuclearOption) {
      return true
    }

    if (isBlacklisted && !isWhitelisted) {
      return true
    }

    if (isBlacklisted && isWhitelisted) {
      const blockedGroup =
        api.BlockedGroupsRepository.getBlockedGroupByDomain(domain)
      const blacklistMatch =
        blockedGroup?.websites.find((site: string) =>
          api.Domain.isMoreGeneralURL(site, domain),
        ) ?? ''
      const isWhitelisted = api.WhitelistRepository.isExists(domain)
      const whitelistMatch = isWhitelisted ? domain : ''

      /* If exact domain is on blacklist and isn't on whitelist, block it */
      if (blacklistMatch === domain && whitelistMatch !== domain) {
        return true
      }

      /* If exact domain is on whitelist and isn't on blacklist, don't block it */
      if (blacklistMatch !== domain && whitelistMatch === domain) {
        return false
      }

      /* If there's no exact match on either list, prefer domain on whitelist if
       * it is more specific than domain on blacklist */
      return DomainParser.isMoreGeneralURL(whitelistMatch, blacklistMatch)
    }

    if (
      isWhitelisted ||
      (NuclearOption.isActive() && !isBlockableByNuclearOption)
    ) {
      return false
    }

    return false
  },

  /**
   * Checks whether the reset time has passed for today
   *
   * @return boolean
   */
  isNewDay: function () {
    let isNewDay = false
    const todayDate = new Date()
    let nowTimestamp = todayDate.getTime()
    let resetTimestamp = api.Settings.get('resetTimestamp')
    let resetTime = this.getResetTime()

    // resetTime was saved as 'null:00' before Aug 3
    if (/null/.test(resetTime)) {
      resetTime = '00:00'
      this.setResetTime(resetTime)
    }

    /* If there's no reset timestamp, set it to the reset time for today (which may or may not have passed) */
    if (
      typeof resetTimestamp === 'undefined' ||
      resetTimestamp === null ||
      resetTimestamp === ''
    ) {
      const resetArray = resetTime.split(':')
      const resetHour = Math.floor(resetArray[0])
      const resetMin = Math.floor(resetArray[1])

      const resetTimestampDate = new Date(
        todayDate.toDateString() + ' ' + resetHour + ':' + resetMin,
      )
      resetTimestamp = resetTimestampDate.getTime()

      this.updateResetTimestamp(resetTime)
    }

    resetTimestamp = Math.floor(resetTimestamp)
    nowTimestamp = Math.floor(nowTimestamp)

    if (nowTimestamp > resetTimestamp) {
      isNewDay = true

      this.updateResetTimestamp(resetTime)
    }

    return isNewDay
  },

  /**
   * Updates badge on icon, turning it yellow, then red, as time runs out
   */
  updateBadge: function (
    totalSecondsRemaining: number,
    nuclearOptionCountdown = false,
  ) {
    let showBadge = false
    let color: BadgeColor | undefined
    const remainingInt = Math.floor(totalSecondsRemaining)
    const remainingText = remainingInt.toString()

    if (nuclearOptionCountdown && remainingInt <= 60) {
      showBadge = true
      color = BadgeColor.BLACK
    } else if (remainingInt <= 30) {
      showBadge = true
      color = BadgeColor.RED
    } else if (remainingInt <= 60) {
      showBadge = true
      color = BadgeColor.YELLOW
    }

    if (showBadge) {
      changeBadgeIcons.showBadge(
        remainingText,
        color,
        this.selectedTabID ?? undefined,
      )
    } else {
      this.hideBadge()
    }
  },

  hideBadge() {
    changeBadgeIcons.hideBadge(this.selectedTabID ?? undefined)
  },

  /**
   * Redirects selected page to redirect url
   */
  killPage: function (url: string): boolean {
    log('killPage', 'Killing page', null, 'INFO')

    api.BlockedGroupsTimer.stopCountdown()

    /* Have to use windows.getLastFocused instead of tab.getSelected, because
     * switching focus to a window opened via an application shortcut doesn't
     * register the application window as the "selected" tab. */
    api.Chrome.Window.getLastFocused((windowObj: chrome.windows.Window) => {
      if (typeof windowObj !== 'undefined') {
        api.Chrome.Tab.getAllInWindow(windowObj.id, (tabs) => {
          for (const wTab in tabs) {
            if (tabs[wTab].highlighted) {
              if (
                api.BlockedGroupsTimer.isMaxTimeAllowedExceeded(url) ||
                (NuclearOption.isActive() && !NuclearOption.hasSmartBomb())
              ) {
                // Update the URL to the redirect page
                const customMsg = api.Settings.get('customStayFocusdMsg') || ''
                api.Chrome.Tab.update(tabs[wTab].id!, {
                  url:
                    this.redirectURL +
                    '?background&customMsg=' +
                    encodeURIComponent(customMsg),
                })
              } else if (
                /* This could probably be a simple else clause, but I'm including conditions out of an
                 * abundance of caution. */
                NuclearOption.isActive() &&
                NuclearOption.hasSmartBomb()
              ) {
                api.PubSub.publish('StayFocusd.smartBomb.activate', {
                  tabID: tabs[wTab].id,
                  smartBomb: NuclearOption.getSmartBomb(),
                })
              }
            }
          }
        })
      }
    })

    return false
  },

  /**
   * Gets the total number of seconds remaining before blacklisted
   * sites are blocked
   */
  getTotalSecondsRemaining: function (): number {
    return api.BlockedGroupsTimer.getTotalSecondsRemaining(this.currentURL!)!
  },

  /**
   * Gets remaining time, formatted like HH:MM:SS
   *
   * @return DisplayTimerResult Remaining time, formatted like HH:MM:SS
   */
  getDisplayTimer: function (): DisplayTimerResult {
    function getTimerForDefaultGroup() {
      const groupName = 'Default'
      const group = API.BlockedGroupsRepository.getByName(groupName)!
      return {
        groupName: groupName,
        time: Time.formatDurationInSeconds(
          group.maxTimeAllowed - group.elapsedTime,
        ),
        nuclear: false,
      }
    }

    // Show nuclear time remaining if the nuclear option is active
    if (API.NuclearOption.isActive()) {
      const nuclearExpiration = API.NuclearOption.getExpiration()
      if (nuclearExpiration) {
        const secondsUntilNuclear = (nuclearExpiration - Date.now()) / SECOND
        return {
          groupName: API.Chrome.Translation.get('nuclear'),
          time: Time.formatDurationInSeconds(secondsUntilNuclear),
          nuclear: true,
        }
      }
    }

    const blockAll = api.BlockedAllWebsites.getTime()
    if (blockAll != null) {
      return {
        groupName: null,
        time: Time.formatDurationInSeconds(
          blockAll.allowedTime - blockAll.elapsedTime,
        ),
        nuclear: false,
      }
    }

    if (this.currentURL == null || this.currentURL == '') {
      return getTimerForDefaultGroup()
    }

    const group = api.BlockedGroupsRepository.getBlockedGroupByDomain(
      this.currentURL,
    )
    if (group == undefined) return getTimerForDefaultGroup()
    return {
      groupName: group.groupName,
      time: Time.formatDurationInSeconds(
        group.maxTimeAllowed - group.elapsedTime,
      ),
      nuclear: false,
    }
  },

  /**
   * Gets today's date as a string: 2010-04-01
   *
   * @return string Today's date
   */
  getDateString: function () {
    const date = new Date()
    const month = date.getMonth() + 1
    const day = date.getDate()
    const year = date.getFullYear()

    return year + '-' + month + '-' + day
  },

  /**
   * Sets the days during which the extension is active
   *
   * @param activeDays Array of days (0-6)
   */
  setActiveDays: function (activeDays: number[]): void {
    let activeDaysString = null

    /* Technically, someone can legitimately have no active days
     * selected, if they de-select all days but today (which is disabled),
     * but then go back tomorrow and deselect that one too. Setting the
     * value to "none" distinguishes this case from someone who tampered
     * with the activeDays settings and just deleted them. */
    if (activeDays.length === 0) {
      activeDaysString = 'none'
    } else {
      activeDaysString = activeDays.join('|')
    }

    log('setActiveDays', 'Setting active days', activeDays)

    api.Settings.set({ activeDays: activeDaysString })
    api.NuclearOption.updateActiveDays(this.getActiveDays(true) as boolean[])
  },

  /**
   * Gets the days during which the extension is active
   *
   * If no days are set, it is active all days by default
   */
  getActiveDays: function (
    asBooleanArray: boolean = false,
  ): number[] | boolean[] {
    const activeDaysString = api.Settings.get('activeDays')

    /* A value of "none" means the activeDays setting is legitimately empty */
    if (activeDaysString === 'none') {
      return []
    }

    /* If the active days are empty, then they need to be re-set to all days */
    if (
      typeof activeDaysString === 'undefined' ||
      activeDaysString === null ||
      activeDaysString.length === 0
    ) {
      this.setActiveDays([0, 1, 2, 3, 4, 5, 6])

      return [0, 1, 2, 3, 4, 5, 6]
    }

    /* If the active days are a single number (when it's just one day), convert to an array */
    if (
      typeof activeDaysString !== 'string' ||
      activeDaysString.indexOf('|') === -1
    ) {
      return [Number(activeDaysString)]
    }

    const activeDaysStrArray = activeDaysString.split('|')
    const activeDaysArray: number[] = []

    /* Convert strings to numbers */
    for (let i = 0; i < activeDaysStrArray.length; i++) {
      activeDaysArray[i] = Number(activeDaysStrArray[i])
    }

    return asBooleanArray
      ? this.getActiveDaysAsWeekArray(activeDaysArray)
      : activeDaysArray
  },

  /**
   * Gets active days as an array where there is an index for each day of the week,
   * with a boolean value in each.
   *
   * @param activeDays Array of active days where the day number is the value, and only active days are in the array
   * @returns {Array}
   */
  getActiveDaysAsWeekArray: function (activeDays: number[]): boolean[] {
    const activeDaysWeekArray: boolean[] = []

    /* Active Days are stored with the day number as the value, so first we need to
     * set all the days in that array to true */
    for (let i = 0; i < activeDays.length; i++) {
      const dayIndex = activeDays[i]

      activeDaysWeekArray[dayIndex] = true
    }

    /* Then we need to loop through all days and set any not already set to true to false */
    for (let j = 0; j < 7; j++) {
      activeDaysWeekArray[j] = activeDaysWeekArray[j] || false
    }

    return activeDaysWeekArray
  },

  /**
   * Checks whether the extension is active today
   */
  isActiveDay: function () {
    const date = new Date()
    const todayDay = date.getDay()
    const activeDays = this.getActiveDays() as number[]

    if (
      typeof activeDays === 'undefined' ||
      activeDays === null ||
      activeDays.length === 0
    ) {
      return false
    }

    return activeDays.inArray(todayDay)
  },

  /**
   * Sets the hours during which the extension is active
   *
   * @param startTime The start time, like 00:00
   * @param endTime The end time, like 23:00
   */
  setActiveHours: function (startTime: string, endTime: string) {
    log('setActiveHours', 'Setting active hours', {
      startTime: startTime,
      endTime: endTime,
    })

    api.Settings.set({ activeHours: startTime + '|' + endTime })
  },

  /**
   * Gets the active hours as an object
   *
   * If the timestamp in the active hours queue has passed, then the
   * queued hours become the active hours. Otherwise, the current
   * active hours are used. If no active hours have been set yet,
   * then the default is set to 00:00 - 23:59 (always active)
   */
  getActiveHours: function (): ActiveHours {
    let startTime: boolean | string = false
    let endTime: boolean | string = false
    const _activeHoursQueue = this.getActiveHoursQueue()

    /* If active hours queue exists, check if queue timestamp has passed */
    if (_activeHoursQueue !== false) {
      const activeHoursQueue = _activeHoursQueue as ActiveHoursQueue
      const todayDate = new Date()
      const currentTimestamp = todayDate.getTime()

      /* If queue timestamp has passed, queued hours become active hours */
      if (
        currentTimestamp > activeHoursQueue.timestamp ||
        this.isFirstInstallAllowanceActive()
      ) {
        startTime = activeHoursQueue.startTime
        endTime = activeHoursQueue.endTime

        this.clearActiveHoursQueue()
        this.setActiveHours(startTime, endTime)
      }
    }

    /* If queued hours aren't being used, get current active hours settings */
    if (startTime === false && endTime === false) {
      let activeHours = api.Settings.get('activeHours')

      /* If no active hours have been defined, set them now */
      if (
        typeof activeHours === 'undefined' ||
        activeHours === null ||
        activeHours === ''
      ) {
        this.setActiveHours('00:00', '23:59')

        activeHours = api.Settings.get('activeHours')
      }

      const activeHoursArray = activeHours.split('|')

      startTime = activeHoursArray[0]
      endTime = activeHoursArray[1]
    }

    const startArray = (startTime as string).split(':')
    const endArray = (endTime as string).split(':')

    return {
      startTime: startTime as string,
      endTime: endTime as string,
      startHour: startArray[0],
      startMin: startArray[1],
      startHourInt: parseInt(startArray[0], 10),
      startMinInt: parseInt(startArray[1], 10),
      endHour: endArray[0],
      endMin: endArray[1],
      endHourInt: parseInt(endArray[0], 10),
      endMinInt: parseInt(endArray[1], 10),
    }
  },

  /**
   * Checks whether the current time is within the active hours
   *
   * @return boolean
   */
  isActiveHour: function () {
    const activeHours = this.getActiveHours()

    const date = new Date()
    const todayHour = date.getHours()
    const todayMin = date.getMinutes()

    if (this.isStartTimeLater(activeHours) === true) {
      return (
        this.isBetween(
          todayHour,
          todayMin,
          activeHours.startHourInt,
          activeHours.startMinInt,
          23,
          59,
        ) ||
        this.isBetween(
          todayHour,
          todayMin,
          0,
          0,
          activeHours.endHourInt,
          activeHours.endMinInt,
        )
      )
    } else {
      return this.isBetween(
        todayHour,
        todayMin,
        activeHours.startHourInt,
        activeHours.startMinInt,
        activeHours.endHourInt,
        activeHours.endMinInt,
      )
    }
  },

  /**
   * Checks whether the time being tested is between the start and end times
   *
   * @param testHour The hour to test
   * @param testMin The minutes to test
   * @param startHour The start hour
   * @param startMin The start minutes
   * @param endHour The end hour
   * @param endMin The end minutes
   */
  isBetween: function (
    testHour: number,
    testMin: number,
    startHour: number,
    startMin: number,
    endHour: number,
    endMin: number,
  ) {
    if (testHour > startHour && testHour < endHour) {
      return true
    }

    if (testHour === startHour && testHour === endHour) {
      return testMin >= startMin && testMin <= endMin
    }

    if (testHour === startHour && testMin >= startMin) {
      return true
    }

    if (testHour === endHour && testMin <= endMin) {
      return true
    }

    return false
  },

  /**
   * Checks whether the start time is later than the end time (meaning that
   * the active hours span midnight)
   *
   * @param activeHours Object created by getActiveHours() method
   */
  isStartTimeLater: function (activeHours: ActiveHours): boolean {
    if (
      activeHours.startHourInt === activeHours.endHourInt &&
      activeHours.startMinInt >= activeHours.endMinInt
    ) {
      return true
    } else if (activeHours.startHourInt > activeHours.endHourInt) {
      return true
    }

    return false
  },

  /**
   * Sets the active hours queue
   *
   * The startTime and endTime is stored, along with a timestamp 24 hours
   * after now. Once that timestamp passes, the queued settings will take
   * effect
   *
   * @param startTime The start time, like 00:00
   * @param endTime The end time, like 23:00
   */
  setActiveHoursQueue: function (startTime: string, endTime: string) {
    const todayDate = new Date()
    const queueDate = todayDate.getTime() + api.Date.hoursToMilliseconds(24)

    const activeHoursQueue = queueDate + '|' + startTime + '|' + endTime

    log('setActiveHoursQueue', 'Setting active hours queue', activeHoursQueue)

    api.Settings.set({ activeHoursQueue: activeHoursQueue })
  },

  /**
   * Gets the active hours queue as an object with timestamp, startTime, and
   * endTime properties
   */
  getActiveHoursQueue: function (): ActiveHoursQueue | boolean {
    const activeHoursQueue = api.Settings.get('activeHoursQueue')

    if (
      typeof activeHoursQueue === 'undefined' ||
      activeHoursQueue === null ||
      activeHoursQueue === ''
    ) {
      return false
    }

    const activeHoursQueueArray = activeHoursQueue.split('|')

    return {
      timestamp: parseInt(activeHoursQueueArray[0]),
      startTime: activeHoursQueueArray[1],
      endTime: activeHoursQueueArray[2],
    }
  },

  /**
   * Removes the active hours queue from local storage
   */
  clearActiveHoursQueue: function () {
    log('clearActiveHoursQueue', 'Clearing active hours queue')

    api.Settings.remove('activeHoursQueue')
  },

  /**
   * Checks whether the extension is active: today is an active day,
   * and the current time is during active hours, or the Nuclear Option
   * is active.
   *
   * @return boolean
   */
  isActive: function () {
    if (NuclearOption.isActive() === true) {
      return true
    }

    return this.isActiveDay() === true && this.isActiveHour() === true
  },

  activated: function () {
    return api.Settings.get('activated', true)
  },

  setActivated: function (value: boolean): boolean {
    if (value === false) {
      if (!confirm(API.Chrome.Translation.get('areYouSure'))) {
        return false
      }

      /* if (API.Settings.get('productivityBypass') === true) {
         alert(API.Chrome.Translation.get('completeChallengeBeforeIncreasingTime'))
         return false
       }*/

      if (!confirm(API.Chrome.Translation.get('disableExtension1'))) {
        return false
      }

      if (!confirm(API.Chrome.Translation.get('disableExtension2'))) {
        return false
      }

      if (!confirm(API.Chrome.Translation.get('disableExtension3'))) {
        return false
      }

      if (!confirm(API.Chrome.Translation.get('disableExtension4'))) {
        return false
      }

      if (!confirm(API.Chrome.Translation.get('disableExtension5'))) {
        return false
      }

      if (!confirm(API.Chrome.Translation.get('disableExtension6'))) {
        return false
      }

      if (!confirm(API.Chrome.Translation.get('disableExtension7'))) {
        return false
      }

      if (!confirm(API.Chrome.Translation.get('disableExtension8'))) {
        return false
      }

      if (!confirm(API.Chrome.Translation.get('disableFinalWarning'))) {
        return false
      }

      alert(API.Chrome.Translation.get('extensionDisabled'))
    }

    api.Settings.set({ activated: value })
    this.checkURL()
    api.Analytics.event(value ? 'EXTENSION_ACTIVATED' : 'EXTENSION_DEACTIVATED')
    return true
  },

  isActivateSwitchShown: function () {
    return api.Settings.get('isActivateSwitchShown', true)
  },

  setActivateSwitchShown: function (value: boolean) {
    api.Settings.set({ isActivateSwitchShown: value })
  },

  /**
   * Updates the reset timestamp to tomorrow's reset time
   *
   * @param resetTime The time of the day to reset, i.e. 04:00
   */
  updateResetTimestamp: function (resetTime: string) {
    const resetArray = resetTime.split(':')
    const resetHour = parseInt(resetArray[0], 10)
    const resetMin = parseInt(resetArray[1], 10)

    const todayResetDate = new Date(
      new Date().toDateString() + ' ' + resetHour + ':' + resetMin,
    )
    const newResetTimestamp = todayResetDate.getTime() + 86400000 // 24 hours in milliseconds

    log(
      'updateResetTimestamp',
      'Updating reset timestamp to: ' + newResetTimestamp,
    )

    api.Settings.set({ resetTimestamp: newResetTimestamp })
  },

  /**
   * Sets the time when the elapsed time counter will be reset
   *
   * @param resetTime The reset time, like 00:00
   */
  setResetTime: function (resetTime: string) {
    log('setResetTime', 'Setting reset time to: ' + resetTime)

    api.Settings.set({ resetTime: resetTime })
  },

  /**
   * Gets the reset time
   *
   * If the timestamp in the reset time queue has passed, then the
   * queued reset time become the current reset time. Otherwise, the
   * current reset time is used. If no reset time has been set yet,
   * then the default is set to 00:00 (midnight)
   */
  getResetTime: function () {
    let resetTime = api.Settings.get('resetTime')
    const resetTimeQueue = this.getResetTimeQueue()
    const defaultResetTime = '00:00'

    /* If reset time queue exists, check if queue timestamp has passed */
    if (resetTimeQueue !== false) {
      const todayDate = new Date()
      const currentTimestamp = todayDate.getTime()
      const resetTimeQueueObj = resetTimeQueue as Record<string, any>

      /* If queue timestamp has passed, queued reset time becomes current reset time */
      if (
        currentTimestamp > resetTimeQueueObj.timestamp ||
        this.isFirstInstallAllowanceActive()
      ) {
        resetTime = resetTimeQueueObj.resetTime

        this.clearResetTimeQueue()
        this.setResetTime(resetTime)
        this.updateResetTimestamp(resetTime)
      }
    }

    if (
      typeof resetTime === 'undefined' ||
      resetTime === null ||
      resetTime === ''
    ) {
      this.setResetTime(defaultResetTime)

      resetTime = defaultResetTime

      this.updateResetTimestamp(resetTime)
    }

    return resetTime
  },

  /**
   * Sets the daily reset time queue
   *
   * The resetTime is stored, along with a timestamp 24 hours
   * after now. Once that timestamp passes, the queued settings
   * will take effect
   *
   * @param resetTime The reset time, like 00:00
   */
  setResetTimeQueue: function (resetTime: string) {
    const todayDate = new Date()
    const queueDate = todayDate.getTime() + api.Date.hoursToMilliseconds(24)

    const resetTimeQueue = queueDate + '|' + resetTime

    log('setResetTimeQueue', 'Setting reset time queue: ' + resetTimeQueue)

    api.Settings.set({ resetTimeQueue: resetTimeQueue })
  },

  /**
   * Gets the reset time queue as an object with timestamp and resetTime properties
   */
  getResetTimeQueue: function (): boolean | ResetTimeQueue {
    const resetTimeQueue = api.Settings.get('resetTimeQueue')

    if (Utils.isEmpty(resetTimeQueue)) {
      return false
    }

    const resetTimeQueueArray = resetTimeQueue.split('|')

    return {
      timestamp: resetTimeQueueArray[0],
      resetTime: resetTimeQueueArray[1],
    }
  },

  /**
   * Removes the reset time queue from local storage
   */
  clearResetTimeQueue: function () {
    log('clearResetTimeQueue', 'Clearing reset time queue')

    api.Settings.remove('resetTimeQueue')
  },

  isFirstInstall: function () {
    return typeof api.Settings.get('firstInstallDate') !== 'string'
  },

  async getFirstSensorTowerInstallDate() {
    let ms = await api.Storage.get('firstSensorTowerInstallDate')
    if (typeof ms !== 'number') {
      ms = Date.now()
      await api.Storage.set({ firstSensorTowerInstallDate: ms })
    }
    return ms
  },

  getOptionsPage: function (): string {
    return Browser.runtime.getURL('options.html')
  },

  isUpdatePopupDisabled: function () {
    const disableUpdatePopup = api.Settings.get('disableUpdatePopup')

    return disableUpdatePopup === true || disableUpdatePopup === 'true'
  },

  setFirstInstallAllowance: function () {
    const date = new Date()

    api.Settings.set({ firstInstallDate: date.toDateString() })

    date.setMinutes(date.getMinutes() + this.firstInstallAllowance)

    log(
      'setFirstInstallAllowance',
      'Setting first install allowance to: ' + date.getTime(),
    )

    api.Settings.set({ firstInstallAllowanceExpiration: date.getTime() })
  },

  isFirstInstallAllowanceActive: function () {
    const todayDate = new Date()
    const expiration = api.Settings.get('firstInstallAllowanceExpiration')

    return expiration > todayDate.getTime()
  },

  /**
   * Finds all HTML elements with a data-i18n attribute and
   * replaces their innerHTML with content from messages.json
   */
  localizeHTML: function (doc: Document) {
    log('localizeHTML', 'Localizing HTML')

    const objects = doc.getElementsByTagName(
      '*',
    ) as HTMLCollectionOf<HTMLElement>

    for (let i = 0; i < objects.length; i++) {
      if (objects[i].dataset && objects[i].dataset.i18n) {
        objects[i].innerHTML = api.Chrome.Translation.get(
          objects[i].dataset.i18n!,
        )
      }
    }
  },
}

/**
 * Checks whether a value is in an array
 *
 * @param needle The value to find
 */
Array.prototype.inArray = function (needle): boolean {
  for (const key in this) {
    if (this.hasOwnProperty(key) && this[key] == needle) {
      return true
    }
  }

  return false
}

export default StayFocusd
