import Logger, { LogLevel } from './Logger'
import { API } from './API'

declare module '~/core/API' {
  interface API {
    Utils: ReturnType<typeof Utils.initAPI>
  }
}

/**
 * ObjectUtils singleton definition
 */
const Utils = {
  firstToLower: function (str: string) {
    return typeof str === 'string' ? str.toLowerCase()[0] + str.slice(1) : str
  },

  firstToUpper: function (str: string) {
    return typeof str === 'string' ? str.toUpperCase()[0] + str.slice(1) : str
  },

  isEmpty: function (value: string) {
    return (
      value === null ||
      value === '' ||
      typeof value === 'undefined' ||
      Object.keys(value).length === 0
    )
  },

  /**
   * Replaces commonly-used Windows 1252 encoded chars that do not exist in
   * ASCII or ISO-8859-1 with ISO-8859-1 cognates.
   *
   * @param text Text to sanitize
   * @returns {*}
   */
  sanitizeMSWordChars: function (text: string) {
    let s = text
    // smart single quotes and apostrophe
    s = s.replace(/[\u2018\u2019\u201A]/g, "'")
    // smart double quotes
    s = s.replace(/[\u201C\u201D\u201E]/g, '"')
    // ellipsis
    s = s.replace(/\u2026/g, '...')
    // dashes
    s = s.replace(/[\u2013\u2014]/g, '--')
    // circumflex
    s = s.replace(/\u02C6/g, '^')
    // open angle bracket
    s = s.replace(/\u2039/g, '<')
    // close angle bracket
    s = s.replace(/\u203A/g, '>')
    // spaces
    s = s.replace(/[\u02DC\u00A0]/g, ' ')

    return s
  },

  /**
   * Returns all the components of a promise separately (promise, resolve, reject).
   * @returns {{ resolve: (value: any) => void; reject: (err: unknown) => void; promise: Promise<any> }}
   */
  getDecoupledPromise<T = void>(): {
    resolve: (t?: T) => void
    reject: (err?: any) => void
    promise: Promise<T>
  } {
    let resolve: (value?: unknown) => void
    let reject: () => void
    const promise = new Promise((res, rej) => {
      resolve = res
      reject = rej
    })
    // TODO - make this work happily with TS
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    return { resolve, reject, promise }
  },

  /**
   * Creates a log function that prefixes the function name with the given scope.
   *
   * @param {string} scope
   * @returns {(funcName: string, message: string, payload: any | undefined, level: 'ERROR' | 'WARN' | 'INFO' | 'DEBUG') => void}
   */
  createScopedLog(scope: string) {
    return (
      funcName: string,
      message: string,
      payload: any = undefined,
      level: LogLevel = 'INFO',
    ) => {
      Logger.log(`${scope}.${funcName}`, message, payload, level)
    }
  },

  /**
   * Returns true if the current window is the top frame of a web page. Iframes will return
   * false.
   */
  isTopFrame() {
    return window.self === window.top
  },

  initAPI(API: API) {
    return API.mixin('Utils', {
      firstToLower: this.firstToLower.bind(this),
      firstToUpper: this.firstToUpper.bind(this),
      isEmpty: this.isEmpty.bind(this),
      sanitizeMSWordChars: this.sanitizeMSWordChars.bind(this),
      getDecoupledPromise: this.getDecoupledPromise.bind(this),
      createScopedLog: this.createScopedLog.bind(this),
      isTopFrame: this.isTopFrame.bind(this),
    })
  },
}

export default Utils
