/**
 * Provides raw API calls for Amplitude.
 */
import { API } from './API'
import GeneralUtils from './GeneralUtils'

declare module '~/core/API' {
  interface API {
    AmplitudeAPI: ReturnType<typeof AmplitudeAPI.initAPI>
  }
}

const log = GeneralUtils.createScopedLog('AmplitudeAPI')

const AmplitudeAPI = {
  baseUrl: 'https://api2.amplitude.com',
  apiKey: '07de81d51a94380a82c075614cd12f6e',

  // eslint-disable-next-line no-undef
  initAPI(API: API) {
    return API.mixin('AmplitudeAPI', {
      uploadEvents: this.uploadEvents.bind(this),
    })
  },

  async uploadEvents(...events: any[]) {
    return await fetch(`${this.baseUrl}/batch`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Accept: '*/*',
      },
      body: JSON.stringify({
        api_key: this.apiKey,
        events,
      }),
    }).catch((err) => {
      log('uploadEvents', 'Failed to upload event', err, 'WARN')
    })
  },
}

export default AmplitudeAPI
