import ObjectUtils from './ObjectUtils'
import GeneralUtils from './GeneralUtils'
import { API } from './API'

const log = GeneralUtils.createScopedLog('Storage')

declare module '~/core/API' {
  interface API {
    Storage: ReturnType<typeof Storage.initAPI>
  }
}

let api: API

export type BucketType = 'SYNC' | 'LOCAL'

/**
 * Storage singleton definition
 */
const Storage = {
  LOCAL: 1 as const,
  SYNC: 2 as const,

  /**
   * Storage map. Map of storage keys to the
   * bucket in which they should be stored
   */
  map: null as Record<string, any> | null,

  /**
   * Cache of values from storage, so we don't have to
   * hit Chrome's storage API on every request.
   */
  cache: {} as Record<string, any>,

  /**
   * Initializes Storage API
   *
   * @param theAPI
   */
  initAPI: function (theAPI: API) {
    api = theAPI

    return api.mixin('Storage', {
      addOnChangeListener: this.addOnChangeListener.bind(this),
      init: this.init.bind(this),
      initAsync: this.initAsync.bind(this),
      get: this.get.bind(this),
      getOrSet: this.getOrSet.bind(this),
      getAll: this.getAll.bind(this),
      set: this.set.bind(this),
      merge: this.merge.bind(this),
      remove: this.remove.bind(this),
      setBucket: this.setBucket.bind(this),
    })
  },

  /**
   * Loads storage map from sync storage
   *
   * @param {() => void} callback Function to call once map is loaded
   */
  init: async function (callback: Function) {
    log('init', 'Initializing storage', null, 'DEBUG')

    this.cache = {}
    this.map = null

    const map: Record<string, any> = await this.getSynced('storageMap')
    log('init', 'Storage initialization complete', map, 'DEBUG')

    if (typeof map !== 'undefined') {
      this.map = map
    } else {
      this.map = {}
      await this.saveMap()
    }

    if (typeof callback === 'function') {
      callback()
    }
  },
  /**
   * @returns {Promise<void>}
   */
  initAsync(): Promise<void> {
    return new Promise((res) => this.init(res))
  },

  /**
   * Gets value from storage
   *
   * @param key The key to retrieve
   * @param callback Function to which retrieved value is returned
   * @param forceRefresh If true, won't pull from cache
   */
  get: async function (key: string, forceRefresh: boolean = false) {
    let data: any
    if (this.inCache(key) && !forceRefresh) {
      data = this.getCached(key)
    } else if (this.inSync(key)) {
      data = await this.getSynced(key)
    } else {
      data = await this.getLocal(key)
    }
    return data
  },

  /**
   * Gets a value from storage, or if the value is null or undefined, sets and returns a
   * default value.
   *
   * @param {string} key The storage key
   * @param {T} defaultValue The value to set and return if the key doesn't exist yet
   * @param {boolean?} forceRefresh If true, won't pull from cache when getting the value
   * @returns {Promise<T>}
   */
  async getOrSet(
    key: string,
    defaultValue: any,
    forceRefresh: boolean = false,
  ) {
    let value = await this.get(key, forceRefresh)
    if (value == null) {
      value = defaultValue
      await this.set({ [key]: value })
    }
    return value
  },

  /**
   * Gets value from storage, forcing it to bypass the cache
   *
   * @param key The key to retrieve
   * @param callback Function to which retrieved value is returned
   */
  refresh: async function (key: string) {
    const data = await this.get(key, true)
    return data
  },

  /**
   * Gets all values from bucket
   *
   * @param _bucket SYNC, LOCAL, or HTML5. If null, gets object with data from all three buckets
   * @param callback Function to which to return values
   */
  getAll: async function (
    _bucket: BucketType | null,
  ): Promise<Record<string, any>> {
    const bucket = (
      typeof _bucket === 'string' ? _bucket.toUpperCase() : _bucket
    ) as BucketType | null

    if (!bucket) {
      const settings: Record<string, any> = {}

      const [sync, local]: Record<string, any>[] = await Promise.all([
        api.Chrome.Storage.getSync(null),
        api.Chrome.Storage.getLocal(null),
      ])
      settings.SYNC = sync
      settings.LOCAL = local

      return settings
    } else if (bucket === 'SYNC') {
      return await api.Chrome.Storage.getSync(null)
    } else if (bucket === 'LOCAL') {
      return await api.Chrome.Storage.getLocal(null)
    }
    return {}
  },

  /**
   * Stores key:value pairs, routing each pair into
   * the appropriate storage bucket
   *
   * @param obj Object contains key:value pairs to save
   * @param callback Function to call once value is set
   * @param bucket Specifies a bucket to which the pairs should be routed.
   *               Overrides (but doesn't replace) values set by setBucket()
   */
  set: async function (obj: any, _bucket: null | string = null) {
    const bucket = typeof _bucket === 'string' ? _bucket.toUpperCase() : null

    const sync: Record<string, any> = {}
    const local: Record<string, any> = {}
    let hasSync = false
    let hasLocal = false

    /* Split pairs into local and sync storage buckets
     * based on the keys' value in the storage map */
    for (const key in obj) {
      if (obj.hasOwnProperty(key)) {
        if (this.inSync(key) || bucket === 'SYNC') {
          sync[key] = obj[key]
          hasSync = true
        } else {
          local[key] = obj[key]
          hasLocal = true
        }
      }
    }

    /**
     * Separately save pairs to sync and local storage,
     * only calling callback when both processes are complete
     */
    const promises: Promise<void>[] = []
    if (hasSync) {
      promises.push(this.setSynced(sync))
    }

    if (hasLocal) {
      promises.push(this.setLocal(local))
    }

    await Promise.all(promises)
  },

  /**
   * Removes value from storage
   *
   * @param key The key to remove
   * @param callback Function to call once value is removed
   * @param bucket Specifies a bucket from which to remove value
   *               Overrides (but doesn't replace) values set by setBucket()
   */
  remove: async function (key: string, bucket?: null | BucketType) {
    if (this.inSync(key) || bucket === 'SYNC') {
      await this.removeSynced(key)
    } else {
      await this.removeLocal(key)
    }

    if (typeof this.map?.[key] !== 'undefined') {
      delete this.map[key]
      await this.saveMap()
    }
  },

  /**
   * Merges object properties of a value in one bucket into the object
   * properties of the value in another bucket.
   *
   * For example:
   *
   * IN SYNC:
   *
   * foo = { a : 1, b : 2 }
   *
   * IN LOCAL:
   *
   * foo = { c : 3 }
   *
   * Merging to SYNC from LOCAL would result in:
   *
   * foo = { a : 1, b : 2, c : 3 }
   *
   * @param key The key to merge
   * @param to The bucket to which to merge
   * @param from The bucket from which to merge
   */
  merge: async function (key: string, to: BucketType, from: BucketType) {
    log(
      'merge',
      'Merging ' + key + ' from ' + from + ' to ' + to,
      null,
      'TRACE',
    )

    if (from === 'SYNC') {
      const source: Record<string, any> = await this.getSynced(key)
      const target: Record<string, any> = await this.getLocal(key)
      await this.doMerge(key, to, target, source)
    } else if (from === 'LOCAL') {
      const source: Record<string, any> = await this.getLocal(key)
      const target: Record<string, any> = await this.getSynced(key)
      await this.doMerge(key, to, target, source)
    }
  },

  /**
   * Merges source object into target object, saving target object
   * to bucket specified by "to" argument
   *
   * @param key The key to use when saving merged object
   * @param to The bucket to which to save the merged object
   * @param target The target object
   * @param source The source object
   * @param callback Function to call when merge is complete
   */
  doMerge: async function (
    key: string,
    to: BucketType,
    target: Record<string, any>,
    source: Record<string, any>,
  ) {
    target = ObjectUtils.isObjLiteral(target) ? target : {}
    source = ObjectUtils.isObjLiteral(source) ? source : {}

    await this.remove(key)
    await this.setBucket(key, to, true)

    const obj: Record<string, any> = {}
    obj[key] = ObjectUtils.merge(target, source, true)

    await this.set(obj)
  },

  /**
   * Adds a key (or array of keys) to the specified storage bucket
   *
   * @param key The key (or array of keys) to add
   * @param bucket The bucket to which the keys should be added
   * @param skipMigration If true, value is not migrated from one bucket to the other
   */
  setBucket: async function (
    _key: string | string[],
    bucket?: BucketType,
    skipMigration: boolean = false,
  ) {
    /* If array of keys is sent, set the bucket for each individually */
    if (this.isArray(_key)) {
      const key = _key as string[]
      for (let i = 0; i < key.length; i++) {
        await this.setBucket(key[i], bucket, skipMigration)
      }
    } else if (typeof _key === 'string') {
      const key = _key as string
      /* Set bucket for individual key */
      if (typeof bucket === 'string') {
        bucket = bucket.toUpperCase() as BucketType
      }

      bucket =
        typeof this[bucket as keyof typeof this] !== 'undefined'
          ? bucket
          : 'LOCAL'

      log(
        'setBucket',
        'Adding ' + key + ' to ' + bucket + ' storage bucket',
        null,
        'TRACE',
      )

      this.map![key] = this[bucket as keyof typeof this]
      await this.saveMap()

      if (skipMigration !== true) {
        await this.migrate(key, bucket!)
      }
    }
  },

  /**
   * Saves storage map to sync storage
   */
  saveMap: async function (_map?: Record<string, any>) {
    const map = (_map ?? this.map) as Record<string, any>

    await this.setSynced({
      storageMap: map,
    })
  },

  /**
   * Migrates value from one storage bucket to another (LOCAL to SYNC or vice versa)
   *
   * @param key The key to migrate
   * @param to The target storage bucket
   */
  migrate: async function (key: string, to: BucketType) {
    const from = to === 'SYNC' ? 'LOCAL' : 'SYNC'

    log(
      'migrate',
      'Migrating ' + key + ' from ' + from + ' to ' + to,
      null,
      'DEBUG',
    )

    if (to === 'SYNC') {
      const value: any = await this.getLocal(key)
      const obj: Record<string, any> = {}
      obj[key] = value

      await this.removeLocal(key)
      await this.setSynced(obj)
    } else if (to === 'LOCAL') {
      const value: any = await this.getSynced(key)
      const obj: Record<string, any> = {}
      obj[key] = value
      await this.removeSynced(key)
      await this.setLocal(obj)
    }
  },

  /**
   * Adds listener for when storage changes
   *
   * @param listener Function to call when storage changes
   */
  addOnChangeListener: function (
    listener: (changes: Record<string, any>, areaName: string) => void,
  ) {
    listener = typeof listener === 'function' ? listener : function () {}

    api.Chrome.Storage.addListener('onChanged', listener)
  },

  /**
   * Checks whether the value has been cached
   *
   * @param key The key to check
   * @returns {boolean}
   */
  inCache: function (key: string) {
    return typeof this.cache[key] !== 'undefined'
  },

  /**
   * Checks whether key is stored in sync storage
   *
   * @param key The key to check
   * @returns {boolean}
   */
  inSync: function (key: string) {
    return this.map![key] === this.SYNC
  },

  /**
   * Checks whether key is stored in local storage
   *
   * @param key The key to check
   * @returns {boolean}
   */
  inLocal: function (key: string) {
    return this.map![key] === this.LOCAL
  },

  /**
   * Stores value in cache
   *
   * @param items Object with key:value pairs to store
   */
  setCached: function (items: Record<string, any>) {
    log('setCached', 'Storing items in cache', items, 'TRACE')

    for (const i in items) {
      if (items.hasOwnProperty(i)) {
        this.cache[i] = items[i]
      }
    }
  },

  /**
   * Gets value from cache
   *
   * @param key The key to retrieve
   * @returns {*}
   */
  getCached: function (key: string) {
    log('getCached', 'Getting ' + key + ' from cache', null, 'TRACE')

    return this.cache[key]
  },

  /**
   * Removes value from cache
   *
   * @param key The key to remove
   */
  removeCached: function (key: string) {
    log('removeCached', 'Removing ' + key + ' from cache', null, 'TRACE')

    delete this.cache[key]
  },

  /**
   * Stores items in sync storage, with a backup stored in localStorage
   *
   * @param items Object with key:value pairs to store
   * @param callback Function to call after value is stored
   */
  setSynced: async function (
    items: Record<string, any>,
    callback: () => void = () => {},
  ) {
    log('setSynced', 'Storing items in sync storage', items, 'TRACE')

    this.setCached(items)

    await api.Chrome.Storage.setSync(items, callback)
  },

  /**
   * Gets value from sync storage
   *
   * @param key The key to retrieve from storage
   * @param callback Function to which retrieved value is returned
   */
  getSynced: async function (key: string) {
    log('getSynced', 'Getting ' + key + ' from sync storage', null, 'TRACE')

    const items: Record<string, any> = await api.Chrome.Storage.getSync(key)
    if (typeof key === 'string') {
      log(
        'getSynced',
        key + ' retrieved from sync storage:',
        items[key],
        'TRACE',
      )

      this.setCached(items)

      return items[key]
    } else {
      log('getSynced', 'Data retrieved from sync storage:', items, 'TRACE')
      return items
    }
  },

  /**
   * Removes value from sync storage
   *
   * @param key The key to remove from storage
   * @param callback Function to call once value is removed
   */
  removeSynced: async function (key: string) {
    log('removeSynced', 'Removing ' + key + ' from sync storage', null, 'TRACE')

    this.removeCached(key)

    await api.Chrome.Storage.removeSync(key)
  },

  /**
   * Stores items in Chrome local storage
   *
   * @param items Object with key:value pairs to store
   * @param callback Function to call after value is stored
   */
  setLocal: async function (items: Record<string, any>) {
    log('setLocal', 'Storing items in Chrome local storage', items, 'TRACE')

    this.setCached(items)

    await api.Chrome.Storage.setLocal(items)
  },

  /**
   * Gets value from Chrome local storage
   *
   * If value is not found in Chrome local storage, tries to find it in HTML5 localStorage
   * (for legacy support). If value is found in HTML5 localStorage, it is saved to Chrome local
   * storage so it will be there from now on.
   *
   * @param key The key to retrieve from storage
   */
  getLocal: async function (key: string) {
    log('getLocal', 'Getting data from Chrome local storage', key, 'TRACE')

    /* Try to get value from Chrome local storage */
    let items: Record<string, any> = await api.Chrome.Storage.getLocal(key)
    if (typeof key === 'string') {
      items = items || {}
      let value = items[key]

      /* If value is not found in Chrome local storage, try to get it from
       * HTML5 localStorage */
      if (typeof value === 'undefined' || value === null) {
        value = await this.getFromLocalStorageBackup(key)
      }

      log('getLocal', key + ' retrieved from local storage:', value, 'TRACE')

      const cacheObj: Record<string, any> = {}
      cacheObj[key] = value

      this.setCached(cacheObj)

      return value
    } else {
      log('getLocal', 'Data retrieved from local storage:', items, 'TRACE')

      return items
    }
  },

  /**
   * Retrieves value from HTML5 local storage backup
   *
   * @param key The key to retrieve
   * @returns {*}
   */
  getFromLocalStorageBackup: async function (key: string): Promise<any> {
    log(
      'getFromLocalStorageBackup',
      'Getting ' + key + ' from HTML5 localStorage',
      null,
      'TRACE',
    )

    let value = null
    const val = await chrome.storage.local.get(key)
    if (val == null) return null
    const lsValue = val[key]

    /* If value found in HTML5 localStorage, save it in Chrome local
     * storage for future retrieval requests */
    if (typeof lsValue !== 'undefined' && lsValue !== null) {
      log('getLocal', key + ' found in HTML5 localStorage:', lsValue, 'TRACE')

      /* Try to decode localStorage value as JSON */
      try {
        value = JSON.parse(lsValue)
      } catch (e) {
        /* If JSON decode fails, use raw localStorage value */
        value = lsValue
      }

      /* Store HTML5 localStorage value in Chrome local storage */
      const obj: Record<string, any> = {}
      obj[key] = value

      await this.setLocal(obj)
    }

    return value
  },

  /**
   * Removes value from Chrome local storage (not HTML5 localStorage)
   *
   * @param key The key to remove from storage
   * @param callback Function to call once value is removed
   */
  removeLocal: async function (key: string) {
    log(
      'removeLocal',
      'Removing ' + key + ' from Chrome local storage',
      null,
      'TRACE',
    )

    this.removeCached(key)

    await api.Chrome.Storage.removeLocal(key)
  },

  /**
   * Checks whether object is an array
   *
   * @param obj The object to check
   * @returns {boolean}
   */
  isArray: function (obj: any) {
    return (
      typeof obj === 'object' && obj !== null && typeof obj.length === 'number'
    )
  },
}

export default Storage
