import { API } from './API'

declare module '~/core/API' {
  interface API {
    Object: ReturnType<typeof ObjectUtils.initAPI>
  }
}

/**
 * ObjectUtils singleton definition
 */
const ObjectUtils = {
  /**
   * Initializes the Object API
   *
   * @param API
   */
  initAPI(API: API) {
    return API.mixin('Object', this)
  },

  /**
   * Merges source object into target object
   *
   * @param target The target object
   * @param source The source object
   * @param deep If true, deep merge is conducted
   * @return {Object} Merged object
   */
  merge: function (
    target: Record<string, any>,
    source: Record<string, any>,
    deep: boolean = false,
  ): Record<string, any> {
    return deep
      ? Object.assign(true, target, source)
      : Object.assign(target, source)
  },

  /**
   * Looks up the value of nested object based on dot notation.
   *
   * For example:
   *
   * var foo = {
   *     bar : {
   *         baz : 'yay!'
   *     }
   * };
   *
   * ObjectUtils.lookup('bar.baz',foo); // returns 'yay!'
   *
   * @param dotPath Dot-notated path to nested object
   * @param sourceObj
   * @return {*}
   */
  lookup: function (dotPath: string, sourceObj: Record<string, any>) {
    const parts = dotPath.split('.')
    const lastPart = parts.pop()!
    const len = parts.length
    let counter = 1
    let current = parts[0]

    while ((sourceObj = sourceObj[current]) && counter < len) {
      current = parts[counter]
      counter += 1
    }

    if (sourceObj) {
      return sourceObj[lastPart]
    }
  },

  /**
   * Checks whether an object is an object literal
   *
   * @param obj
   * @returns {boolean}
   */
  isObjLiteral: function (obj: Record<string, any>) {
    return (
      typeof obj === 'object' &&
      obj !== null &&
      typeof obj.length === 'undefined'
    )
  },

  /**
   * Checks whether an object is an array
   *
   * @param obj
   * @returns {boolean}
   */
  isArray: function (obj: any) {
    return (
      typeof obj === 'object' &&
      obj !== null &&
      typeof obj.length !== 'undefined'
    )
  },

  /**
   * Checks whether a string can be parsed as JSON
   *
   * @param str The string to check
   * @returns {boolean}
   */
  isJSON: function (str: any) {
    try {
      JSON.parse(str)
    } catch (e) {
      return false
    }

    return true
  },

  /**
   * Sorts object by property name
   *
   * @param obj The object to sort
   * @returns {Object} The sorted object
   */
  sort: function (obj: Record<string, any>) {
    const keys = []
    const sortedObj: Record<string, any> = {}

    for (const key in obj) {
      if (obj.hasOwnProperty(key)) {
        keys.push(key)
      }
    }

    keys.sort()

    for (let i = 0; i < keys.length; i++) {
      const key2 = keys[i]
      sortedObj[key2] = obj[key2]
    }

    return sortedObj
  },
}

export default ObjectUtils
