import { API } from './API'

declare module '~/core/API' {
  interface API {
    Date: ReturnType<typeof DateUtils.initAPI>
  }
}

export type AmPm = 'am' | 'pm'
export interface LocalizedDate {
  month: string
  day: string
  year: number
  hours: number
  minutes: string
  ampm: AmPm
}

/**
 * Functions for manipulating dates and times
 *
 * @author              Warren Benedetto <wmbenedetto@gmail.com>
 * @since               March 12, 2011
 * @version             1.1.0
 */
const DateUtils = {
  /**
   * Initializes the Date API
   *
   * @param API
   */
  initAPI(API: API) {
    return API.mixin('Date', this)
  },

  /**
   * Parses military time (i.e. 23:44) into hour, minute, AM/PM,
   * and display time (i.e 11:44 PM)
   *
   * @param militaryTime The military time to parse
   */
  parseMilitaryTime: function (militaryTime: string) {
    const timeArray = militaryTime.split(':')
    let hour = parseInt(timeArray[0], 10)
    const min = parseInt(timeArray[1], 10)

    const ampm = hour >= 12 ? 'pm' : 'am'
    hour = hour >= 12 ? hour - 12 : hour

    const displayHour = hour === 0 ? 12 : hour
    const ampmString =
      ampm === 'pm'
        ? API.Chrome.Translation.get('timePM')
        : API.Chrome.Translation.get('timeAM')

    return {
      hour: this.toTwoDigits(displayHour),
      min: this.toTwoDigits(min),
      ampm: ampm,
      display: displayHour + ':' + this.toTwoDigits(min) + ' ' + ampmString,
    }
  },

  /**
   * Converts timestamp to display date object
   *
   * @param timestamp Unix timestamp
   */
  timestampToDisplayDate: function (timestamp: number): LocalizedDate {
    const date = new Date(timestamp)
    const dateObj = {} as LocalizedDate

    const month = date.getMonth() + 1
    const day = date.getDate()
    const hours = date.getHours()
    const minutes = date.getMinutes()

    dateObj.year = date.getFullYear()
    dateObj.month = this.toTwoDigits(month)
    dateObj.day = this.toTwoDigits(day)
    dateObj.minutes = this.toTwoDigits(minutes)

    dateObj.ampm = (
      hours >= 12
        ? API.Chrome.Translation.get('timePM')
        : API.Chrome.Translation.get('timeAM')
    ) as AmPm
    dateObj.hours = hours > 12 ? hours - 12 : hours

    if (dateObj.hours === 0) {
      dateObj.hours = 12
      dateObj.ampm = API.Chrome.Translation.get('timeAM') as AmPm
    }

    return dateObj
  },

  /**
   * Checks whether the given time has passed
   *
   * @param hour The hour to check
   * @param min The minutes to check
   * @param ampm AM or PM
   * @param nowDate Date obj for the date to test against
   */
  hasTimePassed: function (
    hour: any,
    min: any,
    ampm: AmPm,
    nowDate: Date = new Date(),
  ) {
    hour = ampm === 'pm' ? parseInt(hour, 10) + 12 : hour

    const dateString = nowDate.toDateString()
    const testDate = new Date(dateString + ' ' + hour + ':' + min)

    return testDate < nowDate
  },

  /**
   * Checks how many seconds remain until the given time
   *
   * @param hour The hour to check
   * @param min The minutes to check
   * @param ampm AM or PM
   * @param nowDate Date obj for the date to test against
   */
  secondsUntilTime: function (
    hour: any,
    min: any,
    ampm: AmPm,
    nowDate: Date = new Date(),
  ) {
    hour = ampm === 'pm' ? parseInt(hour, 10) + 12 : hour

    const dateString = nowDate.toDateString()
    const testDate = new Date(dateString + ' ' + hour + ':' + min)

    const msRemaining = testDate.getTime() - nowDate.getTime()

    return Math.floor(msRemaining / 1000)
  },

  /**
   * Checks how many minutes remain until the given time
   *
   * @param hour The hour to check
   * @param min The minutes to check
   * @param ampm AM or PM
   * @param nowDate Date obj for the date to test against
   */
  minutesUntilTime: function (hour: any, min: any, ampm: AmPm, nowDate: Date) {
    const seconds = this.secondsUntilTime(hour, min, ampm, nowDate)

    return Math.floor(seconds / 60)
  },

  /**
   * Gets a Date object for today at the given time
   *
   * @param hour The hour today
   * @param min The minutes today
   * @param ampm AM or PM
   * @param nowDate Date obj for the date to test against
   */
  getTodayDateObj: function (
    hour: any,
    min: any,
    ampm: AmPm,
    nowDate: Date = new Date(),
  ) {
    hour = ampm === 'pm' ? parseInt(hour, 10) + 12 : hour

    const dateString = nowDate.toDateString()

    return new Date(dateString + ' ' + hour + ':' + min)
  },

  /**
   * Converts hours to milliseconds
   *
   * @param hours The hours to convert
   */
  hoursToMilliseconds: function (hours: any) {
    return parseFloat(hours) * 60 * 60 * 1000
  },

  /**
   * Converts time unit to milliseconds, i.e. "2 hours", "12 mins", "4 s"
   *
   * @param num The number of units
   * @param unit The time unit
   */
  timeUnitToMilliseconds: function (num: any, unit: string) {
    if (!this.isValidTimeUnit(unit)) {
      throw new Error(
        '[DateUtils.timeUnitToMilliseconds()] Could not convert time unit to milliseconds. "' +
          unit +
          '" is not a valid time unit.',
      )
    }

    num = parseFloat(num)

    if (this.isValidHourUnit(unit)) {
      return num * 60 * 60 * 1000
    } else if (this.isValidMinUnit(unit)) {
      return num * 60 * 1000
    } else if (this.isValidSecUnit(unit)) {
      return num * 1000
    } else {
      return num
    }
  },

  /**
   * Checks whether string is a properly-formatted time string,
   * in either 12-hour or 24-hour format. (i.e. 10:00 or 14:00)
   *
   * @param time The time to check
   * @param format The time format. Either 12 or 24.
   */
  isValidTime: function (time: any, format: 12 | 24) {
    if (
      typeof time !== 'string' ||
      time.indexOf(':') !== 2 ||
      time.length !== 5
    ) {
      return false
    }

    format = format === 12 ? 12 : 24

    const timeArray = time.split(':')
    const hour = parseInt(timeArray[0])
    const min = parseInt(timeArray[1])

    return (
      min >= 0 &&
      min <= 59 &&
      ((format === 12 && hour >= 1 && hour <= 12) ||
        (format === 24 && hour >= 0 && hour <= 23))
    )
  },

  /**
   * Checks whether time unit is valid.
   *
   * Singular: hour, minute, second, millisecond
   * Plural: hours, minutes, seconds, milliseconds
   * Abbreviated: hr, min, sec, hrs, mins, secs, ms
   *
   * @param unit
   */
  isValidTimeUnit: function (unit: string) {
    return (
      this.isValidHourUnit(unit) ||
      this.isValidMinUnit(unit) ||
      this.isValidSecUnit(unit) ||
      this.isValidMSUnit(unit)
    )
  },

  isValidHourUnit: function (unit: string) {
    return (
      typeof unit === 'string' &&
      (unit.indexOf('hour') === 0 || unit.indexOf('hr') === 0 || unit === 'h')
    )
  },

  isValidMinUnit: function (unit: string) {
    return (
      typeof unit === 'string' && (unit.indexOf('min') === 0 || unit === 'm')
    )
  },

  isValidSecUnit: function (unit: string) {
    return (
      typeof unit === 'string' && (unit.indexOf('sec') === 0 || unit === 's')
    )
  },

  isValidMSUnit: function (unit: string) {
    return (
      typeof unit === 'string' &&
      (unit.indexOf('millis') === 0 || unit === 'ms')
    )
  },

  /**
   * Converts the day number to the day word, full-length or abbreviated (i.e., Monday or Mon)
   *
   * @param dayNum The day number to convert
   * @param abbreviated If true, day will be abbreviated
   * @returns {string}
   */
  dayNumToDay: function (dayNum: number, abbreviated: boolean) {
    if (dayNum < 0 || dayNum > 6) {
      throw new Error(
        '[DateUtils.dayNumToDay()] Day num must be between 0 and 6',
      )
    }

    // @todo Needs to be localized
    const days = [
      'Sunday',
      'Monday',
      'Tuesday',
      'Wednesday',
      'Thursday',
      'Friday',
      'Saturday',
    ]
    const shortDays = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat']

    return abbreviated === true ? shortDays[dayNum] : days[dayNum]
  },

  /**
   * Gets the number of days in a month
   *
   * @param month
   * @param year
   * @returns {number}
   */
  getDaysInMonth: function (month: number, year: any) {
    const todayDate = new Date()
    month = month || todayDate.getMonth()
    // TODO: `Date.getYear()` is not a cross-browser method. It returns the year minus 1900.
    // eslint-disable-next-line
    // @ts-ignore
    year = year || todayDate.getYear()

    return 32 - new Date(year, month, 32).getDate()
  },

  /**
   * Converts seconds to minutes and seconds
   *
   * @param totalSeconds The seconds to convert
   * @returns {string}
   */
  secondsToMinsAndSecs: function (totalSeconds: number) {
    if (totalSeconds / 60 === Math.floor(totalSeconds / 60)) {
      // @todo Needs to be localized
      return totalSeconds / 60 + ' minutes'
    } else {
      // @todo Needs to be localized
      return (
        Math.floor(totalSeconds / 60) +
        ' minutes and ' +
        (totalSeconds - Math.floor(totalSeconds / 60) * 60) +
        ' seconds'
      )
    }
  },

  /**
   * Converts number to two digits
   *
   * @param num The number to convert
   */
  toTwoDigits: function (num: number) {
    return num < 10 ? '0' + num : num.toString()
  },

  /**
   * Gets date in 0000-00-00 format
   *
   * @param date The date object to convert
   */
  getYMD: function (date: Date) {
    date = date || new Date()
    const month = this.toTwoDigits(date.getMonth() + 1)
    const day = this.toTwoDigits(date.getDate())
    const year = date.getFullYear()

    return year + '-' + month + '-' + day
  },
}

export default DateUtils
