import Notification from './Notification'
import { MINUTE, HOUR } from './Time'
import GeneralUtils from './GeneralUtils'
import { API } from './API'
import { AmPm } from './DateUtils'
import SmartBomb from './SmartBomb'
import { badgeIcons } from './badge-icons'

declare module '~/core/API' {
  interface API {
    NuclearOption: ReturnType<typeof NuclearOption.initAPI>
  }
}

const changeBadgeIcons = badgeIcons()
const log = GeneralUtils.createScopedLog('NuclearOption')

let api: API

/**
 * Functions for setting, checking, and executing Nuclear Option
 *
 * @author              Warren Benedetto <wmbenedetto@gmail.com>
 * @since               March 13, 2011
 * @version             1.1.0
 */

export type NuclearContentType = 'wholeSite' | 'smartBomb'
export type NuclearStartType =
  | 'now'
  | 'whenMaxTimeAllowedExceeded'
  | 'atScheduledTime'
export type NuclearFrequency =
  | 'todayOnly'
  | 'activeDays'
  | 'everyWeekday'
  | 'everyDay'
export type NuclearBlockType = 'all' | 'allExceptAllowed' | 'blocked'

export interface NuclearSettings {
  blockType: NuclearBlockType
  contentType: NuclearContentType
  smartBomb: SmartBomb
  blockLength: number
  startType: NuclearStartType
  frequency: NuclearFrequency
  startHour: null | string
  startMin: null | string
  startAmPm: null | AmPm
  activeDays?: boolean[]
  expiration?: number
  lastActiveDate: string
  lastExpiredDate?: string | null
  isCurrentlyActive: boolean
}

const NuclearOption = {
  timer: null as NodeJS.Timer | null, // holds setInterval() timer value
  interval: 1, // how often to check time until nuclear, in seconds
  settings: {} as NuclearSettings,
  initialized: false,

  /**
   * Initializes API
   */
  initAPI: function (theAPI: API) {
    api = theAPI

    return api.mixin('NuclearOption', {
      init: this.init.bind(this),
      initAsync: this.initAsync.bind(this),
      activate: this.activate.bind(this),
      isActive: this.isActive.bind(this),
      getBlockLength: this.getBlockLength.bind(this),
      getBlockType: this.getBlockType.bind(this),
      getContentType: this.getContentType.bind(this),
      getExpiration: this.getExpiration.bind(this),
      getFrequency: this.getFrequency.bind(this),
      getSecondsUntilActive: this.getSecondsUntilActive.bind(this),
      getSmartBomb: this.getSmartBomb.bind(this),
      getStartAmPm: this.getStartAmPm.bind(this),
      getStartHour: this.getStartHour.bind(this),
      getStartMin: this.getStartMin.bind(this),
      getStartType: this.getStartType.bind(this),
      saveSettings: this.saveSettings.bind(this),
      updateActiveDays: this.updateActiveDays.bind(this),
    })
  },

  /**
   * Initializes Nuclear Option
   *
   * @param callback Function to call when initialization is complete
   */
  init: function (callback: Function) {
    log('init', 'Initializing NuclearOption', null, 'DEBUG')

    this.loadSettings()

    this.timer = setInterval(this.tick.bind(this), this.interval * 1000)
    this.initialized = true

    if (typeof callback === 'function') {
      callback()
    }

    api.PubSub.listen(
      'Settings.data.refreshed',
      (message: string, payload: any) => {
        if (payload.changes.nuclearOptionSettings) this.loadSettings()
      },
    )
  },
  initAsync() {
    return new Promise((resolve) => this.init(resolve))
  },

  /**
   * Saves settings
   *
   * @param settings Object containing Nuclear Option settings
   */
  saveSettings: function (settings: NuclearSettings) {
    this.settings = settings
    settings.activeDays = this.calculateActiveDays(settings)

    log('saveSettings', 'Saving settings', settings, 'DEBUG')

    api.Settings.set({ nuclearOptionSettings: settings })
  },

  /**
   * Based on the type of active days selected, figures out exactly
   * which days should be set to active
   *
   * @param settings Object containing Nuclear Option settings
   * @returns {Array} Array of active days
   */
  calculateActiveDays: function (settings: Record<string, any>): boolean[] {
    let activeDays: boolean[] = []

    /* Use the days from the Active Days tab. */
    if (settings.frequency === 'activeDays') {
      activeDays = api.StayFocusd.getActiveDays(true) as boolean[]
    } else if (typeof settings.frequency !== 'undefined') {
      activeDays[0] = settings.frequency === 'everyDay'
      activeDays[1] = settings.frequency.indexOf('every') > -1
      activeDays[2] = settings.frequency.indexOf('every') > -1
      activeDays[3] = settings.frequency.indexOf('every') > -1
      activeDays[4] = settings.frequency.indexOf('every') > -1
      activeDays[5] = settings.frequency.indexOf('every') > -1
      activeDays[6] = settings.frequency === 'everyDay'
    }

    log('calculateActiveDays', 'Calculating active days', activeDays, 'DEBUG')

    return activeDays
  },

  updateActiveDays: function (activeDays: boolean[]) {
    log('updateActiveDays', 'Updating active days', activeDays, 'DEBUG')

    if (!this.initialized) {
      this.init(() => {
        this.updateActiveDays(activeDays)
      })
    }

    this.settings.activeDays = activeDays

    this.saveSettings(this.settings)
  },

  /**
   * Loads settings
   */
  loadSettings: function () {
    log('loadSettings', 'Loading settings', this.settings, 'DEBUG')

    const nuclearOptionSettings = api.Settings.get('nuclearOptionSettings')

    if (nuclearOptionSettings) {
      this.settings = nuclearOptionSettings
    }
  },

  /**
   * Determines whether the Nuclear Option is active.
   *
   * If it should be active and it's not, it is activated here. Likewise,
   * if it shouldn't be active and it is, it's deactivated here.
   */
  isActive: function () {
    if (this.isCurrentlyActive()) {
      if (this.isExpired()) {
        this.deactivate()
      }
    } else if (this.isStarted()) {
      this.activate()
    }

    const isActive = this.isCurrentlyActive()

    log(
      'isActive',
      'Is Nuclear Option active?',
      isActive ? 'YES' : 'NO',
      'TRACE',
    )

    return isActive
  },

  /**
   * Checks whether the Nuclear Option is active today
   */
  isActiveToday: function () {
    const frequency = this.getFrequency()
    const lastActiveDate = this.getLastActiveDate()
    const lastExpiredDate = this.getLastExpiredDate()
    const todayDate = new Date().toDateString()

    if (
      frequency === 'todayOnly' &&
      (lastActiveDate === todayDate || lastActiveDate === null) &&
      lastExpiredDate !== todayDate
    ) {
      return true
    }

    const day = new Date().getDay()
    const activeDays = this.getActiveDays()

    if (activeDays.length === 0) {
      return false
    }

    return activeDays[day]
  },

  /**
   * Checks whether the Nuclear Option has started yet, based on the
   * selected start type.
   */
  isStarted: function (): boolean {
    const startType = this.getStartType()
    const lastActiveDate = this.getLastActiveDate()
    const lastExpiredDate = this.getLastExpiredDate()
    const todayDate = new Date().toDateString()

    if (!this.isActiveToday() || lastExpiredDate === todayDate) {
      /**
       * If the combination of the scheduled time and the block length push the
       * expiration into the next day, then the expiration date will match today's
       * date, even though technically it's the expiration of yesterday's block.
       * Therefore, we have to make sure that the expiration was not only today,
       * but that it happened after the start time for today. If it expired today
       * before it started today, then we know the expiration was actually from
       * yesterday.
       */
      if (startType === 'atScheduledTime') {
        const todayStartTimeDateObj = new Date(
          todayDate +
            ' ' +
            this.getStartHour() +
            ':' +
            this.getStartMin() +
            ' ' +
            this.getStartAmPm(),
        )
        const todayExpirationDateObj = new Date(this.getExpiration()!)

        if (todayExpirationDateObj > todayStartTimeDateObj) {
          return false
        }
      } else {
        return false
      }
    }

    if (startType === 'now') {
      return lastActiveDate === todayDate
    } else if (this.isActiveToday()) {
      if (startType === 'atScheduledTime') {
        return api.Date.hasTimePassed(
          this.getStartHour(),
          this.getStartMin(),
          this.getStartAmPm(),
        ) as boolean
      } else if (startType === 'whenMaxTimeAllowedExceeded') {
        return !!api.BlockedAllWebsites.isMaxTimeAllowedExceeded()
      }
    }
    return false
  },

  /**
   * Activates the Nuclear Option
   */
  activate: function () {
    api.Analytics.event('TOGGLE_NUCLEAR_OPTION', { enabled: true })

    log('activate', 'Activating Nuclear Option')

    const startDate = this.hasScheduledTime()
      ? api.Date.getTodayDateObj(
          this.getStartHour(),
          this.getStartMin(),
          this.getStartAmPm(),
        )
      : new Date()

    this.settings.expiration = startDate.getTime() + this.getBlockLength()
    this.settings.lastActiveDate = new Date().toDateString()
    this.settings.lastExpiredDate = null
    this.settings.isCurrentlyActive = true

    this.saveSettings(this.settings)
    changeBadgeIcons.hideBadge()
  },

  /**
   * Deactivates the Nuclear Option
   */
  deactivate: function () {
    api.Analytics.event('TOGGLE_NUCLEAR_OPTION', { enabled: false })

    log('deactivate', 'Deactivating Nuclear Option')

    this.settings.lastExpiredDate = new Date().toDateString()
    this.settings.isCurrentlyActive = false

    this.saveSettings(this.settings)
  },

  /**
   * Checks whether the Nuclear Option has expired for the day
   */
  isExpired: function () {
    if (this.getExpiration() === null) {
      return true
    }

    const nowDate = new Date()
    const expirationDate = new Date(this.getExpiration()!)

    return expirationDate < nowDate
  },

  /**
   * Checks whether a domain is blockable based on the selected blockType
   *
   * @param isBlacklisted Whether the domain being checked is blacklisted
   * @param isWhitelisted Whether the domain being checked is whitelisted
   */
  isBlockable: function (isBlacklisted: boolean, isWhitelisted: boolean) {
    if (this.isActive() === false) {
      return false
    }

    const blockType = this.getBlockType()

    switch (blockType) {
      case 'all':
        return true

      case 'allExceptAllowed':
        return !isWhitelisted

      case 'blocked':
        return isBlacklisted && !isWhitelisted

      default:
        return false
    }
  },

  hasScheduledTime: function () {
    return this.getStartType() === 'atScheduledTime'
  },

  getSecondsUntilActive: function () {
    return api.Date.secondsUntilTime(
      this.getStartHour(),
      this.getStartMin(),
      this.getStartAmPm(),
    )
  },

  hasSmartBomb: function () {
    return this.getContentType() === 'smartBomb'
  },

  isCurrentlyActive: function () {
    return typeof this.settings.isCurrentlyActive === 'undefined'
      ? false
      : this.settings.isCurrentlyActive
  },

  getSmartBomb: function (): SmartBomb {
    return this.settings.smartBomb ?? {}
  },

  getContentType: function () {
    return typeof this.settings.contentType === 'undefined'
      ? 'wholeSite'
      : this.settings.contentType
  },

  getExpiration: function () {
    return typeof this.settings.expiration === 'undefined'
      ? null
      : Number(this.settings.expiration)
  },

  getLastActiveDate: function () {
    return typeof this.settings.lastActiveDate === 'undefined'
      ? null
      : this.settings.lastActiveDate
  },

  getLastExpiredDate: function () {
    return typeof this.settings.lastExpiredDate === 'undefined'
      ? null
      : this.settings.lastExpiredDate
  },

  getBlockType: function () {
    return typeof this.settings.blockType === 'undefined'
      ? 'blocked'
      : this.settings.blockType
  },

  getBlockLength: function () {
    if (typeof this.settings.blockLength === 'undefined') return HOUR
    else {
      const valueFromStorage = this.settings.blockLength
      //check if value from storage is float or less then 1 minute
      if (
        (Number(valueFromStorage) === valueFromStorage &&
          valueFromStorage % 1 !== 0) ||
        valueFromStorage < MINUTE
      ) {
        return Math.floor(valueFromStorage * 60) * MINUTE
      } else return valueFromStorage
    }
  },

  getStartType: function () {
    return typeof this.settings.startType === 'undefined'
      ? 'now'
      : this.settings.startType
  },

  getStartHour: function () {
    return this.settings.startHour ?? '00'
  },

  getStartMin: function () {
    return this.settings.startMin ?? '00'
  },

  getStartAmPm: function (): AmPm {
    return this.settings.startAmPm ?? 'am'
  },

  getFrequency: function () {
    return this.settings.frequency ?? null
  },

  getActiveDays: function (): boolean[] {
    return this.settings.activeDays ?? []
  },

  tick: function () {
    if (this.hasScheduledTime() && !this.isActive()) {
      const secondsUntilNuclear = this.getSecondsUntilActive()

      log(
        'tick',
        secondsUntilNuclear + ' seconds until Nuclear Option',
        null,
        'TRACE',
      )

      if (Notification.isset(secondsUntilNuclear)) {
        Notification.show('nuclear')
      }
      this.updateBadge()
    } else if (this.isActive()) {
      this.updatePopupTimer()
    }

    return true
  },
  updatePopupTimer() {
    api.StayFocusd.postTimerUpdatedMessage()
  },
  updateBadge: function () {
    const seconds = this.getSecondsUntilActive()
    if (seconds > 0 && seconds < 60) {
      api.StayFocusd.updateBadge(this.getSecondsUntilActive(), true)
    }
  },
}

export default NuclearOption
