import Logger, { LogLevel } from '~/core/Logger'

export enum BadgeColor {
  RED = '#FF0000',
  YELLOW = '#FDD017',
  GREEN = '#009933',
  BLACK = '#000000',
}

export enum BadgeIcon {
  ONBOARDING = '/common/img/icon-red-dot-24.png',
  ALLOWED = '/common/img/icon-green-24.png',
  BLOCKED = '/common/img/icon-red-24.png',
  DEFAULT = '/common/img/icon-24.png',
  NUCLEAR = '/common/img/icon-nuclear-24.png',
  DEACTIVATED = '/common/img/icon-disabled-24.png',
}

/**
 * Alias for Logger.log(). Prepends class name to funcName.
 *
 * @param funcName The name of the function generating the log message
 * @param message The message to log
 * @param payload Data object
 * @param level Log level (ERROR, WARN, INFO, DEBUG)
 */
const log = function (
  funcName: string,
  message: string,
  payload: any,
  level: LogLevel,
) {
  Logger.log(`BadgeIcons.${funcName}`, message, payload, level)
}

export function badgeIcons() {
  /**
   * Shows badge on icon
   *
   * @param _text The badge text
   * @param iconColor The badge's background color name
   * @param _tabID Optional id of tab for which icon should be updated
   */
  function showBadge(
    _text: string | undefined,
    iconColor?: BadgeColor,
    _tabID?: number,
  ) {
    const text = _text || ''
    const tabID = _tabID || undefined

    log('showBadge', 'Showing badge', { text, iconColor, tabID }, 'DEBUG')

    if (iconColor) {
      setBadgeColor(iconColor, tabID)
    }

    setBadgeText(text, tabID)
  }

  /**
   * Hides icon badge
   *
   * @param tabID Optional id of tab for which icon should be updated
   */
  function hideBadge(tabID?: number) {
    tabID = tabID || undefined

    setBadgeText('', tabID)
  }

  /**
   * Sets icon
   *
   * @param type The type of icon to show
   * @param tabID Optional id of tab for which icon should be updated
   */
  async function setIcon(type: BadgeIcon, tabID?: number) {
    tabID = tabID || undefined

    log('setIcon', 'Setting icon', type, 'TRACE')

    if (await isOnboardingIcon()) {
      chrome.action.setIcon({
        tabId: tabID,
        path: BadgeIcon.ONBOARDING,
      })
    } else {
      chrome.action.setIcon({
        tabId: tabID,
        path: type,
      })
    }
  }

  /**
   * Sets badge color
   *
   * @param color The color as RGBA array or hex code
   * @param tabID Optional id of tab for which icon should be updated
   */
  function setBadgeColor(color: string, tabID?: number) {
    log('setBadgeColor', 'Setting badge color', color, 'TRACE')

    tabID = tabID || undefined

    chrome.action.setBadgeBackgroundColor({
      tabId: tabID,
      color: color,
    })
  }

  /**
   * Sets badge text
   *
   * @param text The text to display on the badge
   * @param tabID Optional id of tab for which icon should be updated
   */
  function setBadgeText(text: string, tabID?: number) {
    log('setBadgeText', 'Setting badge text', text, 'TRACE')

    // TODO: Is this a bug? Should it be `tabID ?? undefined` to handle 0 correctly?
    // There are many places in this file that do this, and they all need changed
    tabID = tabID || undefined

    chrome.action.setBadgeText({
      tabId: tabID,
      text: text,
    })
  }

  async function isOnboardingIcon() {
    // const isCompleted = await API.Requirements.isOnboardingCompleted()
    // return !isCompleted
    return false
  }

  return {
    showBadge,
    hideBadge,
    setIcon,
  }
}
