import { API } from '~/core/API'

export const SECOND = 1000
export const MINUTE = 60 * SECOND
export const HOUR = 60 * MINUTE
export const DAY = 24 * HOUR
export const WEEK = 7 * DAY
export const MONTH = 30 * DAY
export const MINUTE_IN_SEC = 60
export const HOUR_IN_SEC = 60 * MINUTE_IN_SEC
export const DAY_IN_SEC = 24 * HOUR_IN_SEC

export const MAX_TIME_ALLOWED_DEFAULT = MINUTE_IN_SEC * 10

export type TimeInterval =
  | typeof DAY
  | typeof HOUR
  | typeof MINUTE
  | typeof SECOND
  | typeof WEEK
  | typeof MONTH

export type DetailedTime = {
  hours: number
  minutes: number
  seconds: number
}

const Time = {
  /**
   * Returns the milliseconds since Epoch for the start of today.
   */
  getToday() {
    return Time.getStartOfDay(new Date())
  },

  /**
   * Returns the milliseconds since Epoch for the end of today.
   */
  getEndOfToday() {
    return Time.getEndOfDay(new Date())
  },

  /**
   * Returns the milliseconds since Epoch for the start of the day of the given
   * @param {Date|string|number} date
   */
  getStartOfDay(date: Date) {
    const d = new Date(date)
    d.setMilliseconds(0)
    d.setSeconds(0)
    d.setMinutes(0)
    d.setHours(0)
    return d.getTime()
  },

  /**
   * Returns the milliseconds since Epoch for the end of the day of the given
   * @param {Date|string|number} date
   */
  getEndOfDay(date: Date) {
    const d = new Date(date)
    d.setMilliseconds(999)
    d.setSeconds(59)
    d.setMinutes(59)
    d.setHours(23)
    return d.getTime()
  },

  formatDurationInSeconds(durationInSec: number) {
    if (durationInSec === 0 || durationInSec < 0) {
      return '00:00'
    }

    const hoursNum = Math.floor(durationInSec / HOUR_IN_SEC)
    const minutesNum = Math.floor((durationInSec - hoursNum * HOUR_IN_SEC) / 60)
    const secondsNum = Math.floor(
      durationInSec - (hoursNum * HOUR_IN_SEC + minutesNum * 60),
    )

    const hours = API.Date.toTwoDigits(hoursNum)
    const minutes = API.Date.toTwoDigits(minutesNum)
    const seconds = API.Date.toTwoDigits(secondsNum)

    return (hours == '00' ? '' : `${hours}:`) + `${minutes}:${seconds}`
  },

  formatTimeToString(time: number) {
    return time >= 0 && time < 10 ? `0${time}` : time
  },

  getTimeFromSeconds(seconds: number): DetailedTime {
    if (seconds < 0)
      return {
        hours: 0,
        minutes: 0,
        seconds: 0,
      }

    const hours = Math.floor(seconds / HOUR_IN_SEC)
    const minutes = Math.floor((seconds - hours * HOUR_IN_SEC) / 60)
    const returnSeconds = seconds - (hours * HOUR_IN_SEC + minutes * 60)
    return {
      hours: hours,
      minutes: minutes,
      seconds: returnSeconds,
    }
  },

  getTimeFromMilliseconds(milliseconds: number) {
    const blockMinInTotal = milliseconds / MINUTE
    const blockHours =
      blockMinInTotal < 60 ? 0 : Math.floor(blockMinInTotal / 60)
    let blockMinutes = 0
    if (blockHours > 0) {
      blockMinutes = blockMinInTotal - blockHours * 60
    } else blockMinutes = Math.floor(blockMinInTotal)
    return {
      hours: blockHours,
      minutes: blockMinutes,
    }
  },

  getTimeInMilliseconds(hours: number, minutes: number) {
    return hours * HOUR + minutes * MINUTE
  },

  getTimeInSeconds(hours: number, minutes: number) {
    return hours * HOUR_IN_SEC + minutes * MINUTE_IN_SEC
  },
}

export default Time
