import { API } from './API'

declare module '~/core/API' {
  interface API {
    Chrome: ReturnType<typeof ChromeAPI.initAPI>
  }
}

const ChromeAPI = {
  /**
   * Initializes the Chrome API
   *
   * @param API
   */
  initAPI(API: API) {
    return API.mixin('Chrome', {
      Extension: {
        addListener: this.exists('runtime')
          ? this.addRuntimeListener.bind(this)
          : this.throwMisusedAPIException,
        connect: this.exists('runtime', 'connect')
          ? chrome.runtime.connect.bind(chrome.runtime)
          : this.throwMisusedAPIException,
        getID: this.exists('runtime')
          ? function () {
              return chrome.runtime.id
            }
          : this.throwMisusedAPIException,
        getURL: this.exists('extension', 'getURL')
          ? chrome.extension.getURL.bind(chrome.extension)
          : this.throwMisusedAPIException,
        reload: this.exists('runtime', 'reload')
          ? chrome.runtime.reload.bind(chrome.runtime)
          : this.throwMisusedAPIException,
        getVersion: this.exists('runtime', 'getManifest')
          ? this.getVersion.bind(this)
          : this.throwMisusedAPIException,
        setUninstallURL: this.exists('runtime', 'setUninstallURL')
          ? chrome.runtime.setUninstallURL.bind(chrome.runtime)
          : this.throwMisusedAPIException,
      },

      Message: {
        on: chrome.runtime.onMessage,
        send: this.exists('runtime', 'sendMessage')
          ? chrome.runtime.sendMessage.bind(chrome.runtime)
          : this.throwMisusedAPIException,
        sendToTab: this.exists('tabs', 'sendMessage')
          ? chrome.tabs.sendMessage.bind(chrome.tabs)
          : this.throwMisusedAPIException,
      },

      Notification: {
        create: this.exists('notifications', 'create')
          ? chrome.notifications.create.bind(chrome.notifications)
          : this.throwMisusedAPIException,
        clear: this.exists('notifications', 'clear')
          ? chrome.notifications.clear.bind(chrome.notifications)
          : this.throwMisusedAPIException,
      },

      Storage: {
        addListener: this.exists('storage')
          ? this.addStorageListener.bind(this)
          : this.throwMisusedAPIException,
        getLocal: this.exists('storage')
          ? chrome.storage.local.get.bind(chrome.storage.local)
          : this.throwMisusedAPIException,
        getSync: this.exists('storage')
          ? chrome.storage.sync.get.bind(chrome.storage.sync)
          : this.throwMisusedAPIException,
        removeLocal: this.exists('storage')
          ? chrome.storage.local.remove.bind(chrome.storage.local)
          : this.throwMisusedAPIException,
        removeSync: this.exists('storage')
          ? chrome.storage.sync.remove.bind(chrome.storage.sync)
          : this.throwMisusedAPIException,
        setLocal: this.exists('storage')
          ? chrome.storage.local.set.bind(chrome.storage.local)
          : this.throwMisusedAPIException,
        setSync: this.exists('storage')
          ? chrome.storage.sync.set.bind(chrome.storage.sync)
          : this.throwMisusedAPIException,
      },

      Tab: {
        addListener: this.exists('tabs')
          ? this.addTabsListener.bind(this)
          : this.throwMisusedAPIException,
        create: this.exists('tabs', 'create')
          ? chrome.tabs.create.bind(chrome.tabs)
          : this.throwMisusedAPIException,
        getAllInWindow: this.exists('tabs', 'query')
          ? this.getAllTabsInWindow.bind(this)
          : this.throwMisusedAPIException,
        getCurrent: this.exists('tabs', 'getCurrent')
          ? chrome.tabs.getCurrent.bind(chrome.tabs)
          : this.throwMisusedAPIException,
        getSelected: this.exists('tabs', 'query')
          ? this.getSelectedTab.bind(this)
          : this.throwMisusedAPIException,
        remove: this.exists('tabs', 'remove')
          ? chrome.tabs.remove.bind(chrome.tabs)
          : this.throwMisusedAPIException,
        update: this.exists('tabs', 'update')
          ? chrome.tabs.update.bind(chrome.tabs)
          : this.throwMisusedAPIException,
        get: this.exists('tabs', 'get')
          ? chrome.tabs.get.bind(chrome.tabs)
          : this.throwMisusedAPIException,
      },

      Translation: {
        get: this.exists('i18n', 'getMessage')
          ? chrome.i18n.getMessage.bind(chrome.i18n)
          : this.throwMisusedAPIException,
      },

      Window: {
        addListener: this.exists('windows')
          ? this.addWindowsListener.bind(this)
          : this.throwMisusedAPIException,
        getLastFocused: this.exists('windows', 'getLastFocused')
          ? chrome.windows.getLastFocused.bind(chrome.windows)
          : this.throwMisusedAPIException,
      },

      Alarms: {
        addListener: this.exists('alarms')
          ? this.addAlarmsListener.bind(this)
          : this.throwMisusedAPIException,
        ensure: this.exists('alarms')
          ? this.ensureAlarm.bind(this)
          : this.throwMisusedAPIException,
      },
    })
  },

  /**
   * Gets the selected tab
   *
   * @param windowID The id of the window to get, or null for current window
   * @param callback Function to which to return the selected tab
   */
  getSelectedTab: function (
    windowID: number | undefined,
    callback: (tab: chrome.tabs.Tab | undefined) => void,
  ) {
    const query: chrome.tabs.QueryInfo = {
      active: true,
    }

    if (windowID) {
      query.windowId = windowID
    } else {
      query.currentWindow = true
    }

    chrome.tabs.query(query, function (tabs) {
      const tab = tabs[0]

      if (typeof callback === 'function') {
        callback(tab)
      }
    })
  },

  /**
   * Gets all the tabs in the specified window, or current window if none specified
   *
   * @param windowID The id of the window to get, or null for current window
   * @param callback Function to which to return an array of tabs
   */
  getAllTabsInWindow: function (
    windowID: number | undefined,
    callback: (tabs: chrome.tabs.Tab[]) => void,
  ) {
    const query: { windowId?: number; currentWindow?: boolean } = {}

    if (windowID) {
      query.windowId = windowID
    } else {
      query.currentWindow = true
    }

    chrome.tabs.query(query, function (tabs) {
      if (typeof callback === 'function') {
        callback(tabs)
      }
    })
  },

  /**
   * Get the extension's version listed in the manifest.
   *
   * @returns {string}
   */
  getVersion() {
    return chrome.runtime.getManifest().version
  },

  /**
   * Ensure an alarm exists.
   *
   * @param {string} name the alarm name.
   * @param {AlarmCreateInfo} alarmInfo The details to create the alarm with if it doesn't exist.
   * @returns {Promise<void>}
   */
  async ensureAlarm(name: string, alarmInfo: any) {
    return new Promise((res: Function) => {
      chrome.alarms.get(name, (alarm) => {
        if (!alarm) chrome.alarms.create(name, alarmInfo)
        res()
      })
    })
  },

  /**
   * Adds listener for chrome.runtime
   *
   * @param listenTo The event to listen for
   * @param listener The function to execute
   */
  addRuntimeListener: function (listenTo: string, listener: Function) {
    this.addListener('runtime', listenTo, listener)
  },

  /**
   * Adds listener for chrome.storage
   *
   * @param listenTo The event to listen for
   * @param listener The function to execute
   */
  addStorageListener: function (listenTo: any, listener: any) {
    this.addListener('storage', listenTo, listener)
  },

  /**
   * Adds listener for chrome.tabs
   *
   * @param listenTo The event to listen for
   * @param listener The function to execute
   */
  addTabsListener: function (listenTo: any, listener: any) {
    this.addListener('tabs', listenTo, listener)
  },

  /**
   * Adds listener for chrome.windows
   *
   * @param listenTo The event to listen for
   * @param listener The function to execute
   */
  addWindowsListener: function (listenTo: any, listener: any) {
    this.addListener('windows', listenTo, listener)
  },

  /**
   * Adds listener for chrome.alarms
   *
   * @param listenTo The event to listen for
   * @param listener The function to execute
   */
  addAlarmsListener(listenTo: any, listener: any) {
    this.addListener('alarms', listenTo, listener)
  },

  /**
   * Adds listener
   *
   * @param _type The type of chrome API
   * @param _listenTo The event to listen for
   * @param listener The function to execute
   */
  addListener: function (_type: string, _listenTo: string, listener: Function) {
    const typed = _type as keyof typeof chrome
    const listenTo = _listenTo as keyof (typeof chrome)[typeof typed]
    if (typeof chrome[typed][listenTo] !== 'undefined') {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      chrome[typed][listenTo].addListener(listener)
    }
  },

  /**
   * Checks whether a Chrome API exists
   *
   * @param _apiName The name of the API to check
   * @param _method The API method to check
   * @returns {*|boolean}
   */
  exists: function (_apiName: string | number, _method?: string | number) {
    const apiName = _apiName as keyof typeof chrome
    const method = _method as keyof (typeof chrome)[typeof apiName]

    let exists = chrome && typeof chrome[apiName] === 'object'

    if (exists && method) {
      exists = typeof chrome[apiName][method] === 'function'
    }

    return exists
  },

  /**
   * Throws exception to warn of improper use of Chrome API
   */
  throwMisusedAPIException: function () {
    throw new Error('Improper use of chrome API in content script!')
  },
}

export default ChromeAPI
