import * as WebExtUsage from '@sensortower/web-ext-usage'
import * as PanelAPI from '@sensortower/panel-api'
import { HOUR, MINUTE } from './Time'
import AppConst from './AppConsts'
import GeneralUtils from './GeneralUtils'
import { API } from './API'

declare module '~/core/API' {
  interface API {
    ScheduleJobs: ReturnType<typeof ScheduleJobs.initAPI>
  }
}

const log = GeneralUtils.createScopedLog('ScheduleJobs')

let api: API

const JobId = {
  UploadWebUsage: '@alarm/upload-web-usage',
}

const SESSIONS_LAST_UPLOADED_AT_STORAGE_KEY =
  '@stayfocusd/sessions-last-uploaded-at-key'
const PAGE_VIEWS_LAST_UPLOADED_AT_STORAGE_KEY =
  '@stayfocusd/page-views-last-uploaded-at-key'

const ScheduleJobs = {
  initAPI(theAPI: API) {
    api = theAPI

    return api.mixin('ScheduleJobs', {
      init: this.init.bind(this),
    })
  },

  init() {
    api.Chrome.Alarms.addListener(
      'onAlarm',
      async (alarm: chrome.alarms.Alarm) => {
        if (alarm.name === JobId.UploadWebUsage) {
          log(`Alarm fired`, alarm.name)

          await this.uploadWebUsageJob()
        }
      },
    )

    api.Chrome.Alarms.ensure(JobId.UploadWebUsage, {
      periodInMinutes: HOUR / MINUTE,
    })
  },

  async uploadWebUsageJob() {
    const usageDatabase = await api.WebUsage.getUsageDatabase()
    const panelApi = PanelAPI.createPanelApiClient({
      ofetchDefaults: {
        baseURL: 'https://stayfocusd.st-panel-api.com',
      },
    })

    const uploader = WebExtUsage.defineWebUsageUploader({
      db: usageDatabase,
      enabled: async () => {
        const uploadEnabled = await api.RemoteConfig.get('uploadWebUsage')
        const hasAcceptedPrivacyPolicy = await api.Storage.get(
          'hasAcceptedPrivacyPolicy',
          true,
        )
        return uploadEnabled && hasAcceptedPrivacyPolicy
      },
      getAppId: () => AppConst.APP_ID,
      dev: !MINIFIED,
      getInstallId: () => api.Storage.get(AppConst.INSTALL_ID_KEY),
      getBirthYear: () => api.Settings.get('age') ?? undefined,
      logger: MINIFIED ? null : console,
      getUploadBatchSize: () => api.RemoteConfig.get('usageUploadBatchSize'),
      api: panelApi,
      sessions: {
        lastUploadedAtStorageKey: SESSIONS_LAST_UPLOADED_AT_STORAGE_KEY,
        onFailure: () => api.Analytics.event('WEB_USAGE_UPLOAD_FAILED'),
        onSuccess: (details) =>
          api.Analytics.event('WEB_USAGE_UPLOAD_COMPLETED', details),
      },
      pageViews: {
        lastUploadedAtStorageKey: PAGE_VIEWS_LAST_UPLOADED_AT_STORAGE_KEY,
        onFailure: () => api.Analytics.event('PAGE_VIEWS_UPLOAD_FAILED'),
        onSuccess: (details) =>
          api.Analytics.event('PAGE_VIEWS_UPLOAD_COMPLETED', details),
        getUploadAllowlist: () => api.RemoteConfig.get('pageViewAllowlist'),
      },
    })

    await uploader.uploadAll()
  },
}

export default ScheduleJobs
