import Logger from '~/core/Logger'
import { API } from './API'

declare module '~/core/API' {
  interface API {
    IncrementalRollout: ReturnType<typeof IncrementalRollout.initAPI>
  }
}

let api: API

function getRemoteConfigFeatures() {
  return api.RemoteConfig.get('incrementalRollout')
}

/**
 * Creates an object with incremental rollout utils for a given feature.
 * @param {String} feature The name of the feature.
 */
const defineFeature = (feature: string) => ({
  storageKey: `@incrementalRollout/${feature}`,

  /**
   * Returns the random number that was generated for this user.
   * @returns {Promise<Number>} A random number between 0 and 1.
   */
  async getMyPercent() {
    return api.Storage.getOrSet(this.storageKey, Math.random())
  },

  /**
   * Returns the percentage of users that should have the feature enabled.
   * @returns {Promise<Number>} A number between 0 and 1.
   */
  async getEnabledPercent() {
    const features = await getRemoteConfigFeatures()
    return features[feature] ?? 0
  },

  /**
   * @returns {Promise<Boolean>} Whether or not the feature is enabled.
   */
  async isEnabled() {
    const enabledPercent = await this.getEnabledPercent()
    const myPercent = await this.getMyPercent()
    return myPercent < enabledPercent
  },

  /**
   * Prints debug info to the console.
   */
  async debug() {
    Logger.log(
      'IncrementalRollout',
      `Incremental rollout info:`,
      {
        isEnabled: await this.isEnabled(),
        myPercent: await this.getMyPercent(),
        enabledPercent: await this.getEnabledPercent(),
        remoteConfig: await getRemoteConfigFeatures(),
      },
      'DEBUG',
    )
  },

  /**
   * @deprecated Should only be used only for debugging purposes.
   *
   * Enables the feature for this user.
   *
   * @example
   * API.IncrementalRollout.AdFinder.enable();
   */
  async enable() {
    await api.Storage.set({ [this.storageKey]: -1 })
  },

  /**
   * @deprecated Should only be used only for debugging purposes.
   *
   * Disables the feature for this user.
   *
   * @example
   * API.IncrementalRollout.AdFinder.disable();
   */
  async disable() {
    await api.Storage.set({ [this.storageKey]: 2 })
  },

  /**
   * @deprecated Should only be used only for debugging purposes.
   *
   * Randomly assigns a new percentage to this user.
   *
   * @example
   * API.IncrementalRollout.AdFinder.randomize();
   */
  async randomize() {
    await api.Storage.set({ [this.storageKey]: Math.random() })
  },
})

const IncrementalRollout = {
  initAPI(theAPI: API) {
    api = theAPI

    return api.mixin('IncrementalRollout', {
      AdFinder: defineFeature('ad-finder'),
    })
  },
}

export default IncrementalRollout
