import Storage, { BucketType } from './Storage'
import { API } from './API'
import GeneralUtils from './GeneralUtils'

declare module '~/core/API' {
  interface API {
    Settings: ReturnType<typeof Settings.initAPI>
  }
}

const log = GeneralUtils.createScopedLog('Settings')

let api: API

const keys = [
  'activeDays',
  'activeHours',
  'activeHoursQueue',
  'blacklist',
  'challengeRequired',
  'challengeText',
  'customStayFocusdMsg',
  'disableActivityMonitor',
  'disableSync',
  'disableUpdatePopup',
  'firstInstallDate',
  'firstInstallAllowanceExpiration',
  'hideAllowSiteLink',
  'maxTimeAllowed',
  'notificationSettings',
  'nuclearOptionSettings',
  'productivityBypass',
  'resetTimeQueue',
  'resetTimestamp',
  'resetTime',
  'whitelist',
  'hasAcceptedToS', // boolean | undefined | null
  'hasAcceptedPrivacyPolicy', // boolean | undefined | null
  'explicitlyDeclinedPrivacyPolicy', // boolean | undefined | null
  'is18OrOlder', // boolean | undefined | null
  'age', // number | undefined | null
  'isNewUser', // boolean | undefined | null
  'promptBirthYearAt', // number | undefined | null
  'promptBirthYearInterval', // number | undefined | null
  'promptReview', // boolean | undefined | null
  'promptReviewAt', // number | undefined | null
  'promptReviewInterval', // number | undefined | null
  'promptSurvey', // boolean | undefined | null
  'promptSurveyAt', // number | undefined | null
  'promptSurveyInterval', // number | undefined | null
  'debugActivityMonitor', // boolean | undefined | null - will be used for e2e testing
  'blockedGroups', // BlockGroup[] | undefined
  'isBlockAllWebsites', // boolean | undefined | null
  'blockAllWebsitesAllowedTime', // number | undefined | null
  'blockAllWebsitesElapsedTime', // number | undefined | null
  'showedInitialTutorial', // boolean | undefined | null
  'desktopPromoDismissed', // boolean | undefined | null
  'whitelistNew', // string[] | undefined | null
  'activated', // boolean | undefined | null
  'isActivateSwitchShown',
]

type SettingsKey = (typeof keys)[number]

/**
 * Settings singleton definition
 */
const Settings = {
  /* Maximum number of backup versions to maintain */
  MAX_BACKUP_VERSIONS: 5,
  keys: keys,
  cache: {} as Record<string, any>,
  backupKeys: [] as string[],
  initialized: false,

  /**
   * Initializes Settings API
   *
   * @param theAPI
   */
  initAPI: function (theAPI: API) {
    api = theAPI

    return api.mixin('Settings', {
      init: this.init.bind(this),
      initAsync: this.initAsync.bind(this),
      get: this.get.bind(this),
      set: this.set.bind(this),
      setAsync: this.setAsync.bind(this),
      refresh: this.refresh.bind(this),
      remove: this.remove.bind(this),
      restoreBackup: this.restoreBackup.bind(this),
      isInitialized: this.isInitialized.bind(this),
    })
  },

  /**
   * Initializes settings by retrieving keys from storage and
   * caching them in this object.
   *
   * @param {() => void} callback Function to call when all settings are loaded
   */
  init: function (callback: Function | undefined) {
    /* If settings are already initialized, just execute callback */
    if (this.initialized) {
      log('init', 'Settings already initialized', this.keys, 'DEBUG')

      if (typeof callback === 'function') {
        callback()
      }
    } else {
      log('init', 'Initializing Settings', this.keys, 'DEBUG')

      this.initialized = true
      this.cache = {}

      Storage.init(() => {
        this.initBackups()
        this.refresh(callback)
      })
    }
  },
  /**
   * @returns {Promise<void>}
   */
  initAsync() {
    return new Promise((res) => this.init(res))
  },

  /**
   * Refreshes settings from Storage
   *
   * @param callback Function to call when all settings have been refreshed
   */
  refresh: function (callback?: Function) {
    log('refresh', 'Refreshing Settings', this.keys, 'TRACE')

    let initCount = 0
    const numKeys = this.keys.length

    const refreshKey = (key: string) => {
      /* Gets each value and caches it in memory */
      Storage.refresh(key).then((value: any) => {
        this.cache[key] = typeof value !== 'undefined' ? value : null
        initCount++

        if (initCount === numKeys) {
          log('refresh', 'Settings refreshed', null, 'TRACE')

          if (typeof callback === 'function') {
            callback()
          }
        }
      })
    }

    for (let i = 0; i < numKeys; i++) {
      refreshKey(this.keys[i])
    }
  },

  /**
   * Retrieves settings value
   *
   * @param key The setting to retrieve
   * @param defaultValue
   * @returns {*}
   */
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  get: function (key: SettingsKey, defaultValue?: any) {
    if (!this.inCache(key)) {
      throw new Error(
        '[Settings.get()] ' +
          key +
          ' is not registered as a key in Settings.js',
      )
    }

    return this.cache[key] !== null ? this.cache[key] : defaultValue
  },

  /**
   * Stores setting value(s)
   *
   * @param items Object with key:value pairs to store
   * @param callback Function to call once settings are saved
   * @param bucket Specifies a bucket to which the value(s) should be routed.
   * @param allowUnregisteredSettings If true, unregistered settings will be allowed
   */
  set(
    items: Record<SettingsKey, any>,
    callback?: Function,
    bucket?: BucketType | null,
    allowUnregisteredSettings?: boolean,
  ) {
    this.saveBackup()

    for (const _key in items) {
      const key = _key as SettingsKey
      if (items.hasOwnProperty(key)) {
        if (!this.inCache(key) && !allowUnregisteredSettings) {
          throw new Error(
            '[Settings.get()] ' +
              key +
              ' is not registered as a key in Settings.js',
          )
        }

        this.cache[key] = items[key]
      }
    }

    Storage.set(items, bucket).then(() => {
      if (typeof callback === 'function') {
        callback()
      }
    })
  },
  /**
   * Same as `set`, but async.
   */
  setAsync(
    items: Record<SettingsKey, any>,
    bucket?: BucketType,
    allowUnregisteredSettings?: boolean,
  ): Promise<unknown> {
    return new Promise((res) =>
      this.set(items, res, bucket, allowUnregisteredSettings),
    )
  },

  /**
   * Removes value from settings
   *
   * @param key The key to remove
   */
  remove: function (key: SettingsKey) {
    if (this.inCache(key)) {
      this.cache[key] = null
    }

    Storage.remove(key)
  },

  /**
   * Initializes backups by loading backup keys and deleting
   * and orphaned backups
   */
  initBackups: function () {
    Storage.get('backupKeys').then((backupKeys: string | undefined) => {
      this.backupKeys =
        typeof backupKeys === 'string' ? JSON.parse(backupKeys) : []

      this.deleteOrphanedBackups(this.backupKeys)
    })
  },

  /**
   * Saves backup of settings
   */
  saveBackup: async function () {
    const key = 'backup-' + new Date().getTime()

    log('saveBackup', 'Saving backup: ' + key, this.cache, 'TRACE')

    /* If the max number of backup versions has been reached, prune backups */
    if (this.backupKeys.length >= this.MAX_BACKUP_VERSIONS) {
      this.pruneBackups()
    }

    this.backupKeys.unshift(key)

    const obj: Record<string, any> = {}
    obj[key] = JSON.stringify(this.cache)
    obj.backupKeys = JSON.stringify(this.backupKeys)

    await Storage.setBucket(key)
    await Storage.set(obj)
    log('saveBackup', 'Backup saved', null, 'TRACE')
  },

  /**
   * Restores settings backup
   *
   * @returns {boolean}
   * @param _version
   */
  restoreBackup: function (_version = undefined) {
    const version = _version || this.backupKeys.shift()

    if (!version) {
      log('restoreBackup', 'No backup available to restore', null, 'WARN')
      return false
    }

    log('restoreBackup', 'Restoring backup', version, 'DEBUG')

    Storage.get(version).then((backupString?: string) => {
      log('restoreBackup', 'Backup loaded', version, 'DEBUG')

      if (typeof backupString === 'string') {
        try {
          const backup = JSON.parse(backupString)

          Storage.set(backup).then(async () => {
            await Storage.remove(version)

            log('restoreBackup', 'Backup restored', version, 'DEBUG')

            await Storage.set({ backupKeys: JSON.stringify(this.backupKeys) })
            api.Chrome.Extension.reload()
          })
        } catch (e) {
          log(
            'restoreBackup',
            'Backup failed. Error parsing backup JSON.',
            version,
            'DEBUG',
          )

          /* Try to roll back to earlier version */
          this.restoreBackup()
        }
      } else {
        log(
          'restoreBackup',
          "Backup failed. Backup wasn't a string.",
          version,
          'DEBUG',
        )

        /* Try to roll back to earlier version */
        this.restoreBackup()
      }
    })
    return true
  },

  /**
   * Deletes oldest backup from storage
   */
  pruneBackups: function () {
    if (this.isArray(this.backupKeys)) {
      const oldKey = this.backupKeys.pop()

      log('pruneBackups', 'Pruning backup: ' + oldKey, null, 'TRACE')

      Storage.remove(oldKey!)
    }
  },

  /**
   * Deletes any backups in HTML5 localStorage which aren't currently in the
   * backupKeys array
   *
   * @param backupKeys Array of keys for valid backups
   */
  deleteOrphanedBackups: function (backupKeys: string[]) {
    log('deleteOrphanedBackups', 'Deleting orphaned backups', null, 'TRACE')

    const validBackupKeys: Record<string, any> = {}

    /* Create object to use as lookup for backup keys from local storage */
    for (let i = 0; i < backupKeys.length; i++) {
      validBackupKeys[backupKeys[i]] = 1
    }

    /* Gets storage map */
    Storage.get('storageMap').then((data: Record<string, any>) => {
      let hasChanged = false

      for (const item in data) {
        if (data.hasOwnProperty(item)) {
          /* If it's a backup key but isn't a valid backup, delete it */
          if (item.indexOf('backup-') === 0 && !(item in validBackupKeys)) {
            delete data[item]
            hasChanged = true
          }
        }
      }

      /* Updated storage map if needed */
      if (hasChanged) {
        Storage.saveMap(data)
      }
    })
  },

  /**
   * Checks whether setting is in cache
   *
   * @param key The setting to check
   * @returns {boolean}
   */
  inCache: function (key: SettingsKey) {
    return typeof this.cache[key] !== 'undefined'
  },

  /**
   * Checks whether object is an array
   *
   * @param obj The object to check
   * @returns {boolean}
   */
  isArray: function (obj: any) {
    return (
      typeof obj === 'object' && obj !== null && typeof obj.length === 'number'
    )
  },

  isInitialized() {
    return this.initialized
  },
}

export default Settings
