import { MINUTE_IN_SEC } from './Time'
import { API } from './API'

declare module '~/core/API' {
  interface API {
    BlockedAllWebsites: ReturnType<typeof BlockedAllWebsites.initAPI>
  }
}

let api: API

export type BlockTime = {
  allowedTime: number
  elapsedTime: number
}

const BlockedAllWebsites = {
  initAPI(theAPI: API) {
    api = theAPI

    return api.mixin('BlockedAllWebsites', {
      getTime: this.getTime.bind(this),
      isMaxTimeAllowedExceeded: this.isMaxTimeAllowedExceeded.bind(this),
      getTotalSecondsRemaining: this.getTotalSecondsRemaining.bind(this),
      clearElapsedTime: this.clearElapsedTime.bind(this),
    })
  },

  /**
   * Get allowed time and elapsed time if all websites are blocked
   * @returns {boolean | null}
   */
  getTime(): BlockTime | null {
    const blockAllWebsites = api.Settings.get('isBlockAllWebsites') ?? false
    if (blockAllWebsites) {
      const allowedTime =
        api.Settings.get('blockAllWebsitesAllowedTime') ?? MINUTE_IN_SEC * 10
      const elapsedTime = api.Settings.get('blockAllWebsitesElapsedTime') ?? 0
      return {
        allowedTime: allowedTime,
        elapsedTime: elapsedTime,
      }
    }
    return null
  },

  /**
   * Is max time allowed exceeded if all websites are blocked. Return null if all websites have not been blocked
   * @returns {boolean | null}
   */
  isMaxTimeAllowedExceeded() {
    const blockTimeForAllObj = this.getTime()
    if (blockTimeForAllObj != null) {
      return blockTimeForAllObj.elapsedTime >= blockTimeForAllObj.allowedTime
    }

    return null
  },

  /**
   * Return total seconds remaining if all websites are blocked. Return null if all websites have not been blocked
   * @returns {number | null}
   */
  getTotalSecondsRemaining() {
    const blockTimeForAllObj = this.getTime()
    if (blockTimeForAllObj != null) {
      return blockTimeForAllObj.allowedTime - blockTimeForAllObj.elapsedTime
    }

    return null
  },

  /**
   * Clear elapsed time for all websites
   * @returns {Promise<void>}
   */
  async clearElapsedTime() {
    await api.Storage.set({ blockAllWebsitesElapsedTime: 0 })
  },
}

export default BlockedAllWebsites
