import ObjectUtils from './ObjectUtils'
import GeneralUtils from './GeneralUtils'

const log = GeneralUtils.createScopedLog('API')

/**
 * API singleton
 *
 * Mixins can be added to the API by calling:
 *
 * K.API.mixin('MixinName',{ someFunc : function(){ ... } })
 * K.API.mixin('foo.bar.Baz',{ someFunc : function(){ ... } })
 * K.API.mixin({ someFunc : function(){ ... } })
 * K.API.mixin(function(){ return { ... } })
 */

// TODO - Refactor to classes for standard public/private, autogenerate this interface from the classes array in CoreAPI.ts, and remove inline module augmentation
export interface API {
  mixin<TFunctions>(name: keyof API, functions: TFunctions): TFunctions
  mixin<T>(entries: T): T
}

const api = (() => {
  const _api = (() => {
    let mixins = {} as API
    let mixinTemp: null | Record<string, any> = null

    const mixin = function (...args: any[]) {
      let name = ''
      let mixinObj: null | Record<string, any> = null

      if (args.length === 0) {
        throw new TypeError('Mixin has no arguments')
      }

      log('mixin', 'Adding mixin to API', args, 'DEBUG')

      if (args.length === 2 && typeof args[0] === 'string') {
        name = args[0]

        if (typeof args[1] === 'object' && args[1] !== null) {
          mixinObj = args[1]
        } else if (typeof args[1] === 'function') {
          mixinObj = args[1]()
        }

        if (mixinObj) {
          /* If mixin name uses dot notation, recursively call mixin() to create nested object */
          if (name.indexOf('.') > -1) {
            const parts = name.split('.')
            const lastPart = parts.pop()!
            const result: Record<string, any> = {}

            /* If it's not already there, look up current value of the nested object
             * referenced by this dot notation, and persist it in a temp variable */
            if (!mixinTemp) {
              mixinTemp = ObjectUtils.lookup(name, mixins)
            }

            /* Deep merge the mixin with the existing object (if any) */
            result[lastPart] = mixinTemp
              ? ObjectUtils.merge(mixinTemp, mixinObj, true)
              : mixinObj

            /* Recursively call mixin() to create nested object */
            mixin(parts.join('.'), result)
          } else {
            /* Otherwise, assign mixin to name on root mixins object */
            /* If the mixin already exists, merge this mixin into it */
            const mixinName = name as keyof API
            if (name in mixins) {
              mixins[mixinName] = ObjectUtils.merge(
                mixins[mixinName],
                mixinObj,
              ) as any
            } else {
              /* Otherwise, add mixin directly to root mixins obj */
              mixins[mixinName] = mixinObj as any
            }

            mixinTemp = null
          }
        }
      } else if (
        args.length === 1 &&
        typeof args[0] === 'object' &&
        args[0] !== null
      ) {
        mixins = ObjectUtils.merge(mixins, args[0]) as API
      } else if (args.length === 1 && typeof args[0] === 'function') {
        mixinObj = args[0]()

        if (typeof mixinObj === 'object' && mixinObj !== null) {
          mixins = ObjectUtils.merge(mixins, mixinObj) as API
        }
      }
    }

    mixin({
      mixin: mixin,
    })

    return mixins
  })()
  return _api
})()

export const API = api
