import { isHostnameBlockedPredicate } from '~/utils/isHostnameBlocked'
import { API } from './API'
import { MAX_TIME_ALLOWED_DEFAULT, MINUTE_IN_SEC } from './Time'

declare module '~/core/API' {
  interface API {
    BlockedGroupsRepository: ReturnType<typeof BlockedGroupsRepository.initAPI>
  }
}

const GROUPS_IN_STORAGE = 'blockedGroups'
const BLACK_LIST_IN_STORAGE = 'blacklist'
const MAX_TIME_ALLOWED_IN_STORAGE = 'maxTimeAllowed'
const DEFAULT_GROUP_NAME = 'Default'

// maxTimeAllowed & elapsedTime store in seconds
export interface BlockGroup {
  groupName: string
  maxTimeAllowed: number
  websites: string[]
  elapsedTime: number
}

let api: API
let _groups: BlockGroup[] = []

const BlockedGroupsRepository = {
  initAPI(theAPI: API) {
    api = theAPI

    return api.mixin('BlockedGroupsRepository', {
      init: this.init.bind(this),
      add: this.add.bind(this),
      get: this.get.bind(this),
      getByName: this.getByName.bind(this),
      update: this.update.bind(this),
      updateWebsites: this.updateWebsites.bind(this),
      addWebsite: this.addWebsite.bind(this),
      clearElapsedTime: this.clearElapsedTime.bind(this),
      incrementElapsedTime: this.incrementElapsedTime.bind(this),
      delete: this.delete.bind(this),
      isBlockable: this.isBlockable.bind(this),
      getBlockedGroupByDomain: this.getBlockedGroupByDomain.bind(this),
      getBlockedGroupsByDomain: this.getBlockedGroupsByDomain.bind(this),
      migrate: this.migrate.bind(this),
      importBlockedsites: this.importBlockedsites.bind(this),
    })
  },

  async init() {
    const syncDisabled = API.Settings.get('disableSync') === true

    if (syncDisabled) {
      const data = await api.Chrome.Storage.getSync(GROUPS_IN_STORAGE)
      const hasDataForMigration = !!data[GROUPS_IN_STORAGE]
      await api.Storage.setBucket(
        GROUPS_IN_STORAGE,
        'LOCAL',
        !hasDataForMigration,
      )
    } else {
      const data = await api.Chrome.Storage.getLocal(GROUPS_IN_STORAGE)
      const hasDataForMigration = !!data[GROUPS_IN_STORAGE]
      await api.Storage.setBucket(
        GROUPS_IN_STORAGE,
        'SYNC',
        !hasDataForMigration,
      )
    }

    this.getGroups()
    this.addListeners()
  },

  async getGroups() {
    const groups = api.Settings.get(GROUPS_IN_STORAGE)
    _groups = groups == null ? [] : groups
  },

  /**
   * Add a new BlockGroup
   * @returns {Promise<void>}
   */
  async add(
    groupName: string,
    maxTime: any,
    websites: string[],
    elapsedTime: any,
  ) {
    const existingGroup = _groups.find((x) => x.groupName == groupName)
    if (existingGroup == undefined) {
      const newGroup: BlockGroup = {
        groupName: groupName,
        maxTimeAllowed: maxTime,
        websites: websites,
        elapsedTime: elapsedTime == undefined ? 0 : elapsedTime,
      }

      _groups.push(newGroup)
      await this.saveChanges()
      API.Analytics.event('CREATE_BLOCKED_GROUP')
    }
  },

  /**
   * Add all blocked groups
   * @returns {BlockGroup[]}
   */
  get() {
    return _groups
  },

  /**
   * Add blocked group by name
   * @returns {BlockGroup}
   */
  getByName(groupName: string) {
    return _groups.find((x) => x.groupName == groupName)
  },

  /**
   * Update blocked group by name
   * @returns {Promise<void>}
   */
  async update(
    sourceGroupName: string,
    groupName: string,
    maxTime: any,
    websites: string[],
  ) {
    const existingGroup = _groups.find((x) => x.groupName == sourceGroupName)
    if (typeof existingGroup === 'undefined') return

    if (existingGroup.groupName != groupName)
      API.Analytics.event('UPDATE_BLOCKED_GROUP_NAME')

    if (existingGroup.groupName != 'Default')
      existingGroup.groupName = groupName

    if (existingGroup.maxTimeAllowed != maxTime)
      API.Analytics.event('UPDATE_BLOCKED_GROUP_MAX_TIME_ALLOWED', {
        minutes: maxTime / 60,
      })

    existingGroup.maxTimeAllowed = maxTime
    existingGroup.websites = websites
    await this.saveChanges()
  },

  /**
   * Update websites in blocked group by name
   * @returns {Promise<void>}
   */
  async updateWebsites(groupName: string, websites: string[]) {
    const existingGroup = _groups.find((x) => x.groupName == groupName)
    if (typeof existingGroup === 'undefined') return
    existingGroup.websites = websites
    await this.saveChanges()
  },

  /**
   * Add a website to blocked group by name
   * Returns the flag whether the site was successfully added or not
   * @returns {Promise<boolean>}
   */
  async addWebsite(groupName: string, website: string) {
    const existingGroup = _groups.find((x) => x.groupName == groupName)
    if (typeof existingGroup === 'undefined') return
    if (existingGroup.websites.find((x) => x == website) == null) {
      existingGroup.websites.push(website)
      await this.saveChanges()
      return true
    }
    return false
  },

  /**
   * Clear elapsed time for all groups
   * @returns {Promise<void>}
   */
  async clearElapsedTime() {
    const groups = this.get()
    groups.forEach((group) => (group.elapsedTime = 0))
    await this.saveChanges()
    await api.BlockedAllWebsites.clearElapsedTime()
  },

  /**
   * Delete blocked group
   * @returns {Promise<void>}
   */
  async delete(groupName: string): Promise<void> {
    const targetGroupIndex = _groups.findIndex((x) => x.groupName == groupName)
    if (targetGroupIndex > -1) {
      _groups.splice(targetGroupIndex, 1)
      await this.saveChanges()
      API.Analytics.event('DELETE_BLOCKED_GROUP')
    }
  },

  /**
   * Is domain contains in any blocked groups
   * @returns {void}
   */
  isBlockable(domain: string) {
    domain = domain.toLowerCase()
    return (
      _groups.find((group) =>
        group.websites.find((site) => isHostnameBlockedPredicate(site, domain)),
      ) != undefined
    )
  },

  /**
   * If domain contains in any blocked groups return blocked group
   * @returns {BlockGroup}
   */
  getBlockedGroupByDomain(domain: string) {
    if (domain == null) return null
    domain = domain.toLowerCase()
    return _groups.find((group) =>
      group.websites.find((site) => isHostnameBlockedPredicate(site, domain)),
    )
  },

  /**
   * Returns all blocked groups that contain provided domain
   * @returns {BlockGroup[]}
   */
  getBlockedGroupsByDomain(domain: string) {
    return _groups.filter((group) =>
      group.websites.find((site) => isHostnameBlockedPredicate(site, domain)),
    )
  },

  /**
   * Migrate all blocled websites to blocked group. Create a new default blocked group.
   * @returns {Promise<void>}
   */
  async migrate() {
    if (this.needMigrate()) {
      const websitesObj = api.Settings.get(BLACK_LIST_IN_STORAGE)
      let maxTimeAllowedInMinutes = api.Settings.get(
        MAX_TIME_ALLOWED_IN_STORAGE,
      )
      if (maxTimeAllowedInMinutes == null)
        maxTimeAllowedInMinutes = MAX_TIME_ALLOWED_DEFAULT
      else maxTimeAllowedInMinutes = maxTimeAllowedInMinutes * MINUTE_IN_SEC
      let elapsedTime = await api.Storage.get('elapsedTime')
      if (isNaN(elapsedTime) || elapsedTime == null) elapsedTime = 0
      if (websitesObj != null) {
        const websites = Object.keys(websitesObj)
        await this.add(
          DEFAULT_GROUP_NAME,
          maxTimeAllowedInMinutes,
          websites,
          elapsedTime,
        )
      } else
        await this.add(
          DEFAULT_GROUP_NAME,
          maxTimeAllowedInMinutes,
          [],
          elapsedTime,
        )
    }
  },

  needMigrate() {
    return api.Settings.get(GROUPS_IN_STORAGE) == null
  },

  /**
   * Move all blocked websites to the default groups after import settings
   * @returns {Promise<void>}
   */
  async importBlockedsites() {
    // we shoud import blockes websites only if user have done export settings in the old version of the extension that doesn't have blocked groups
    // if storage has blocked groups than we shoudn't take blocked websites
    _groups = []
    const websitesObj = api.Settings.get(BLACK_LIST_IN_STORAGE)
    if (websitesObj != null) {
      const websites = Object.keys(websitesObj)
      let maxTimeAllowedInMinutes = api.Settings.get(
        MAX_TIME_ALLOWED_IN_STORAGE,
      )
      if (maxTimeAllowedInMinutes == null)
        maxTimeAllowedInMinutes = MAX_TIME_ALLOWED_DEFAULT
      else maxTimeAllowedInMinutes = maxTimeAllowedInMinutes * MINUTE_IN_SEC
      await this.add(DEFAULT_GROUP_NAME, maxTimeAllowedInMinutes, websites, 0)
    }
  },

  async incrementElapsedTime(groupName: string) {
    const group = this.getByName(groupName)
    if (!group) return
    group.elapsedTime += 1
    await this.saveChanges()
  },

  async saveChanges() {
    await api.Storage.set({
      blockedGroups: _groups,
    })
  },

  addListeners() {
    API.PubSub.listen(
      'Settings.data.refreshed',
      async (message: string, payload: any) => {
        if (payload.changes.blockedGroups || payload.changes.disableSync) {
          if (payload.changes.disableSync) {
            if (payload.disableSync === false) {
              await api.Storage.setBucket(GROUPS_IN_STORAGE, 'SYNC')
            } else {
              await api.Storage.setBucket(GROUPS_IN_STORAGE, 'LOCAL')
            }
          }
          this.getGroups()
        }
      },
    )
  },
}

export default BlockedGroupsRepository
